export function closeModal(updateState) {
	updateState({ modalOpened: false });
}

function toggleSpinner(toggle) {
	return function spinner(updateState) {
		updateState({ loading: toggle });
	};
}

export const showLoader = toggleSpinner(true);
export const hideLoader = toggleSpinner(false);

export function showAlert(updateState, component, messageType, messageText) {
	updateState({
		path: component + '.showAlert',
		operation: 'set',
		value: true
	});

	updateState({
		path: component + '.alertMessage',
		operation: 'set',
		value: { type: messageType, message: messageText }
	});
}

export function updateFieldsReadOnlyState(updateState, fieldAry, flagValue) {
	fieldAry.forEach(fieldIndex => {
		updateState({
			path: `initiateApproval.fields[${fieldIndex}].readonly`,
			operation: 'set',
			value: flagValue
		});
	});
}

export function updateFieldsHiddenState(updateState, fieldAry, flagValue) {
	fieldAry.forEach(fieldIndex => {
		updateState({
			path: `initiateApproval.fields[${fieldIndex}].hidden`,
			operation: 'set',
			value: flagValue
		});
	});
}

export function clearFieldsValues(updateState, fieldAry) {
	fieldAry.forEach(fieldIndex => {
		updateState({
			path: `initiateApproval.fields[${fieldIndex}].value`,
			operation: 'set',
			value: ''
		});
	});
}

export function getMergedFieldValues(state, updatedState = {}) {
	return state.initiateApproval.fields.reduce((fieldValueObj, field) => {
		fieldValueObj[field.name] = updatedState[field.name]
			? updatedState[field.name].newValue
			: field.value;
		return fieldValueObj;
	}, {});
}

export function disableSubmitButton(updateState, flagValue) {
	updateState([
		{
			path: `initiateApproval.primaryAction.disabled`,
			value: flagValue,
			operation: 'set'
		},
		{
			path: `initiateApproval.manageSubmit`,
			value: flagValue,
			operation: 'set'
		}
	]);
}
