import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';
import { ELEMENTS } from '../../constants';
import actionHandlers from './actions';

/**
 *
 *
 *
 * ```jsx
 * <sn-legal-task-details />
 * ```
 *
 * @seismicElement sn-legal-task-details
 * @summary Wrapper component including instructions and checklist component.
 */
createCustomElement(ELEMENTS.SN_LEGAL_TASK_DETAILS, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		sysId: {
			reflect: true
		},
		table: {
			reflect: true
		},
		position: {
			default: 'absolute'
		}
	},
	actionHandlers,
	styles
});
