import { ACTION_TYPES, LABELS } from './constants';
import '@servicenow/now-button';
import '@servicenow/now-empty-state';
import '@servicenow/now-checkbox';
import '@servicenow/now-input';
import { keyUtils } from '@devsnc/library-form';
import './sn-legal-checklist-item';
import { itemSpinner } from './spinner';
import { addItem } from './effects';

// import { delay } from '../../utils';
// import { findTabbableNodes } from '@servicenow/behavior-key-binding';

const view = (state, { dispatch, updateState }) => {
	const {
		properties: {
			checklistId,
			checklistTitle,
			table,
			readOnly,
			sysId,
			position
		},
		createNew,
		items,
		lastItemOrder,
		isItemLoading,
		value,
		isLoading,
		isError
	} = state;

	// TODO: Implement ally
	// const focusButton = () => {
	// 	delay(100).then(() => {
	// 		const tabbableNodes = findTabbableNodes(hostEl);
	// 		if (tabbableNodes.length > 0) {
	// 			//	tabbableNodes[tabbableNodes.length - 1].focus();
	// 		}
	// 	});
	// };

	const onKeyPress = event => {
		keyUtils.onEnter(event, () => {
			addItem({ lastItemOrder, checklistId, dispatch, updateState, value });
			//focusButton();
		});

		keyUtils.onEscape(event, () => {
			updateState({
				createNew: false
			});
			//focusButton();
		});
	};

	const renderAddItemButton = () => {
		if (!createNew && !readOnly && !isItemLoading) {
			return (
				<now-button-bare
					iconNameStart="plus-outline"
					label={LABELS.ADD_ITEM}
					size="md"
					config-aria={{
						'aria-label': LABELS.ADD_ITEM
					}}
					on-click={() => updateState({ createNew: true })}
					variant="secondary"
				/>
			);
		}
		return null;
	};

	const renderAddItemInputGroup = () => {
		if (createNew && !isItemLoading) {
			return (
				<div className="legal-checklist-create-new">
					<div className="legal-checklist-input-group">
						<now-checkbox readonly={true} disabled={true} />
						<now-input
							name="create-new"
							className="legal-checklist-input"
							autofocus={true}
							placeholder={LABELS.ADD_ITEM}
							on-keydown={onKeyPress}
							type="text"
						/>
					</div>
					<now-button-bare
						iconNameStart="plus-outline"
						label={LABELS.ADD_ITEM}
						size="md"
						disabled={true}
						variant="secondary"
					/>
				</div>
			);
		}
		return null;
	};

	const renderChecklistItems = () =>
		items.map(item => {
			return (
				<sn-legal-checklist-item
					key={item.id}
					id={item.id}
					label={item.name}
					order={item.order}
					completed={item.complete}
					readOnly={readOnly}
					checklistId={item.checklistId}
				/>
			);
		});

	const renderContent = () => {
		if (isLoading) {
			return (
				<now-empty-state
					widget={LABELS.CHECKLIST}
					type="loading"
					variant="small"
				/>
			);
		} else {
			if (isError) {
				return <now-empty-state type="empty" variant="small" />;
			} else if (!checklistId) {
				return (
					<div className="legal-checklist-empty">
						<p className="legal-checklist-empty-label">
							{LABELS.NO_CHECKLIST_AVAILABLE}
						</p>
						{!readOnly ? (
							<now-button
								label={LABELS.CREATE_NEW_CHECKLIST}
								size="md"
								variant="secondary"
								on-click={() => {
									updateState({
										isLoading: true
									});
									dispatch(ACTION_TYPES.CREATE_NEW_CHECKLIST, {
										data: {
											table,
											document: sysId
										}
									});
								}}
							/>
						) : null}
					</div>
				);
			} else {
				return (
					<section className="legal-checklist-items">
						<div id={`checklist-${checklistId}`}>
							{renderChecklistItems()}
							{itemSpinner(isItemLoading)}
							{renderAddItemInputGroup()}
							{renderAddItemButton()}
						</div>
					</section>
				);
			}
		}
	};
	return (
		<main style={{ position }}>
			<header>
				{checklistTitle && (
					<h2 className="legal-checklist-title">{checklistTitle}</h2>
				)}
			</header>
			{renderContent()}
		</main>
	);
};

export default view;
