import {
	cancelApproval,
	cancelApprovalSuccessEffect,
	cancelApprovalFailureEffect,
	getPendingApproval,
	getPendingApprovalSuccessEffect,
	getPendingApprovalFailureEffect
} from './effects';

import {
	CANCEL_APPROVAL,
	CANCEL_APPROVAL_FAILED,
	CANCEL_APPROVAL_SUCCESS,
	GET_PENDING_APPROVAL,
	GET_PENDING_APPROVAL_SUCCESS,
	GET_PENDING_APPROVAL_FAILED
} from './constants';

export default {
	[CANCEL_APPROVAL]: {
		...cancelApproval(),
		stopPropagation: true
	},

	[CANCEL_APPROVAL_SUCCESS]: {
		effect: cancelApprovalSuccessEffect,
		stopPropagation: true
	},

	[CANCEL_APPROVAL_FAILED]: {
		effect: cancelApprovalFailureEffect,
		stopPropagation: true
	},

	[GET_PENDING_APPROVAL]: {
		...getPendingApproval(),
		stopPropagation: true
	},

	[GET_PENDING_APPROVAL_SUCCESS]: {
		effect: getPendingApprovalSuccessEffect,
		stopPropagation: true
	},

	[GET_PENDING_APPROVAL_FAILED]: {
		effect: getPendingApprovalFailureEffect,
		stopPropagation: true
	}
};
