import { ACTION_TYPES } from './constants';

import {
	addToExistingMatter,
	addToExistingMatterSuccessEffect,
	addToExistingMatterFailureEffect
} from './effects';

export default {
	[ACTION_TYPES.ADD_TO_EXISTING_MATTER]: {
		...addToExistingMatter(),
		stopPropagation: true
	},
	[ACTION_TYPES.ADD_TO_EXISTING_MATTER_SUCCESS]: {
		effect: addToExistingMatterSuccessEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.ADD_TO_EXISTING_MATTER_FAILURE]: {
		effect: addToExistingMatterFailureEffect,
		stopPropagation: true
	}
};
