import '@servicenow/now-checkbox';
import '@servicenow/now-input';
import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import { itemSpinner } from '../spinner';
import { t } from 'sn-translate';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { ACTION_TYPES, DROPDOWN_ITEMS } from '../constants';

const view = (state, { dispatch, updateState }) => {
	const {
		isHovering,
		isDropDownOpen,
		isLoading,
		editing,
		properties: { id, label, checklistId, completed, readOnly }
	} = state;

	const onItemEnter = () => updateState({ isHovering: true });
	const onItemLeave = () => updateState({ isHovering: false });

	const showDropDownCondition = !readOnly && !completed;
	const editingCondition = editing && !completed && !readOnly;

	const renderEditChecklistItem = () => {
		if (editingCondition) {
			return (
				<div className="legal-checklist-item-input-container">
					<now-checkbox
						name={id + '-input'}
						disabled={true}
						readonly={true}
						checked={completed}
					/>
					<now-input
						name="editing"
						value={label}
						className="legal-checklist-item-input"
						autofocus={true}
						type="text"
					/>
				</div>
			);
		}
		return null;
	};

	const renderDropDown = () => {
		if (showDropDownCondition) {
			return (
				<now-dropdown
					icon="ellipsis-v-outline"
					bare
					class={{
						visible: isHovering || isDropDownOpen,
						'legal-checklist-item-dropdown': true
					}}
					size="sm"
					config-aria={{
						'aria-label': t('Options')
					}}
					hide-caret
					select="none"
					items={DROPDOWN_ITEMS}
				/>
			);
		}
		return null;
	};

	const renderChecklistItem = () => {
		if (!editingCondition) {
			return (
				<div
					className="legal-checklist-item"
					on-focusin={onItemEnter}
					on-mouseenter={onItemEnter}
					on-focusout={onItemLeave}
					on-mouseleave={onItemLeave}
					id={id + '-item'}
					role="listitem">
					<div className="legal-checklist-item-container">
						<now-checkbox
							name={id + '-input'}
							manageChecked={true}
							configAria={{
								'aria-labelled-by': `label-${id}`
							}}
							disabled={readOnly ? true : false}
							checked={completed}
						/>
						<label
							id={`label-${id}`}
							on-click={() => {
								if (!readOnly) {
									updateState({ isLoading: true });
									dispatch(ACTION_TYPES.UPDATE, {
										id,
										checklistId,
										completed: !completed
									});
								}
							}}
							className={`legal-checklist-item-label ${completed &&
								'legal-checklist-item-label--strike'}`}>
							{label}
						</label>
						{itemSpinner(isLoading)}
					</div>
					{renderDropDown()}
				</div>
			);
		}
		return null;
	};

	return (
		<Fragment>
			{renderEditChecklistItem()}
			{renderChecklistItem()}
		</Fragment>
	);
};

export default view;
