import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '../sn-legal-modal-fieldset';
import actionHandlers from './actions';
import { properties } from './properties';
import { ELEMENTS } from '../../constants';
import { getFieldsetProps, getPartialInitialState } from './actionMapUtils';

/**
 * Create matter component provides a modal to choose whether matter should be created with template
 * or without template
 *
 * ```jsx
 * <sn-legal-modal-fieldset-connected
 *      title="Create Matter"
 *      table-name="sn_lg_ops_digital_forensic_request"
 *      sys-id="f632fe7b73a37300844489b954f6a7f2"
 * ```
 *
 * @seismicElement sn-legal-modal-fieldset-connected
 * @summary Modal fieldset connected component.
 */

const view = state => (
	<sn-legal-modal-fieldset
		{...getFieldsetProps(state.properties.action, state)}
		scriptedModal={state.properties.scriptedModal}
		opened={state.modalOpened}
		loading={state.loading}
	/>
);

createCustomElement(ELEMENTS.SN_LEGAL_MODAL_FIELDSET_CONNECTED, {
	renderer: {
		type: snabbdom,
		view
	},
	properties,
	initialState: {
		...getPartialInitialState(),
		modalOpened: true,
		loading: false
	},
	actionHandlers
});
