import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './styles.scss';
import { LABELS, CHECKLIST_STATES } from './constants';
import actionHandlers from './actions';
import view from './view';
import { ELEMENTS } from '../../constants';

/**
 * Checklist component provides a todo list of items which can be attached to
 * any record and have features like adding, deleting, mark as complete etc...
 *
 * ```jsx
 * <sn-legal-checklist
 *      title="Checklist"
 *      table-name="sn_lg_ops_request"
 *      sys-id="f632fe7b73a37300844489b954f6a7f2"
 * ```
 *
 * @seismicElement sn-legal-checklist
 * @summary Checklist component for workspace.
 */
createCustomElement(ELEMENTS.SN_LEGAL_CHECKLIST, {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		items: [],
		isLoading: true,
		isError: false,
		isBlurred: false,
		createNew: false,
		isItemLoading: false,
		value: '',
		checklistState: CHECKLIST_STATES.Normal,
		lastItemOrder: 0,
		hostEl: null
	},
	properties: {
		/**
		 * Title of the checklist component.
		 * @type {string}
		 */
		checklistTitle: {
			reflect: true,
			default: LABELS.CHECKLIST
		},
		/**
		 * Sys id of the Checklist
		 * @type {string}
		 */
		checklistId: {
			reflect: true
		},
		/**
		 * Record sys id to which the checklist is attached to (example task sys id)
		 * @type {string}
		 */
		sysId: {
			reflect: true
		},
		/**
		 * Name of the table to which Checklist is attached to
		 * @type {string}
		 */
		table: {
			reflect: true
		},

		readOnly: {
			default: false,
			selectable: true
		},

		position: {
			default: 'absolute'
		}
	},
	actionHandlers,
	styles
});
