import { BASE_API } from '../constants';

export {
	sendErrorNotification,
	refreshList,
	openRecord
} from './helperActions';

// Write utility functions common across all components here

/**
 *
 *
 * @param {*} duration - Timeout duration in ms
 * @returns {Promise}
 *
 * @example
 *
 * delay(1000).
 *   then(console.log('Prints after 1 second.'));
 */
export const delay = duration =>
	new Promise(resolve => setTimeout(resolve, duration));

/**
 *
 * @param {DOM Element} elm - element on which visibility is checked(only vertical)
 * @param {function} callback - code that runs only if elm is on in viewport
 */
export const onlyOnViewport = (elm, callback) => {
	let timer;
	timer = setInterval(() => {
		const flag = isElmInViewport(elm);
		if (flag) {
			clearInterval(timer);
			callback();
		}
	}, 800);
};

/**
 *
 *
 * @param {DOM element} elm
 * @returns {Boolean} - Returns true or false based on whether element is there on not.
 */
const isElmInViewport = elm => {
	const rect = elm.getBoundingClientRect();
	if (rect.width === 0 && rect.height === 0 && rect.top === 0) {
		// display:none scenario
		return false;
	}
	const windowHeight =
		window.innerHeight || document.documentElement.clientHeight;

	const vertInView = rect.top <= windowHeight && rect.top + rect.height >= 0;
	return vertInView;
};

export const logErrorEffect = {
	effect: coeffects => {
		const {
			action: { type, payload }
		} = coeffects;

		// eslint-disable-next-line no-console
		console.error(type, payload);
	}
};

export const effectWithInterceptors = (effect, interceptors) => {
	return {
		...effect,
		interceptors
	};
};

/**
 * Returns the API path
 * @param {String} scriptName
 * @param {String} methodName
 * @return {String} - Returns the full API path
 */
export const getAPIPath = (scriptName, methodName, baseApi = BASE_API) =>
	`${baseApi}/${scriptName}/${methodName}`;

/**
 * Adds class to DOM Element
 * @param {DOMElement} el
 * @param {String} className
 */
export const addClassName = (el, className) => {
	if (!el.classList.contains(className)) {
		el.classList.add(className);
	}
};

/**
 * Removes class from DOM Element
 * @param {DOMElement} el
 * @param {String} className
 */
export const removeClassName = (el, className) => {
	el.classList.remove(className);
};

export const getElementFromShadowRoot = (elm, id) => {
	if (elm.shadowRoot) {
		return elm.shadowRoot.getElementById(id);
	}
	return null;
};

export const getActiveElement = hostEl => {
	if (hostEl.shadowRoot && hostEl.shadowRoot.activeElement) {
		return getActiveElement(hostEl.shadowRoot.activeElement);
	}

	return hostEl;
};
