import { getValidFieldTypes } from './fields/fieldTypeUtils';
import { t } from 'sn-translate';

export const properties = {
	/**
	 * A unique name for the component instance.
	 * name will be included in all action payloads.
	 * @type {string}
	 */
	name: {
		default: true,
		required: true,
		schema: { type: 'string' }
	},
	/**
	 * When true removes the modal wrapper and just renders the content.
	 * Useful when using scripting modals in UI actions
	 * @type {boolean}
	 */
	scriptedModal: {
		default: false,
		schema: { type: 'boolean' }
	},
	/**
	 * When true, the modal is open.
	 * @type {boolean}
	 */
	opened: {
		default: true,
		schema: { type: 'boolean' }
	},
	/**
	 * When true, hides the body and shows loader.
	 * @type {boolean}
	 */
	loading: {
		default: false,
		schema: { type: 'boolean' }
	},
	/**
	 * When true, allows display of alert messages at the top.
	 * @type {boolean}
	 */
	showAlert: {
		default: false,
		schema: { type: 'boolean' }
	},
	/**
	 * The text content for the modal header.
	 * @type {string}
	 */
	headerLabel: {
		default: '',
		schema: { type: 'string' }
	},
	/**
	 * When set to true, it closes the modal after dispatching the
	 * SN_FIELDSET#MODAL_SUBMIT_CLICKED action.
	 * @type {boolean}
	 */
	closeOnSubmit: {
		default: false,
		schema: { type: 'boolean' }
	},
	/**
	 * When set to true, it doesn't make the submit disabled on
	 * error conditions or required fields. It should be handled manually.
	 * @type {boolean}
	 */
	manageSubmit: {
		default: false,
		schema: { type: 'boolean' }
	},
	/**
	 * Sets the size of the modal container. Most sizes automatically expand to
	 * fill the viewport when the screen size is small.
	 * @type {('sm'|'md'|'lg'|'fullscreen')}
	 */
	size: {
		default: 'sm',
		schema: { type: 'string', enum: ['sm', 'md', 'lg', 'fullscreen'] }
	},
	/**
	 * An object that defines the primary button in the modal footer.
	 * The keys of object are properties for `now-button`. See
	 * `now-button` for more documentation on these properties.
	 * @type {{ label: string, variant: string}[]}
	 */
	primaryAction: {
		default: {
			variant: 'primary',
			label: 'Save'
		},
		schema: {
			type: 'object',
			properties: {
				label: { type: 'string' },
				variant: { type: 'string' }
			}
		}
	},
	/**
	 * An object that defines the secondary button in the modal footer.
	 * The keys of object are properties for `now-button`. See
	 * `now-button` for more documentation on these properties.
	 * @type {{ label: string, variant: string}[]}
	 */
	secondaryAction: {
		default: {
			variant: 'secondary',
			label: t('Cancel')
		},
		schema: {
			type: 'object',
			properties: {
				label: { type: 'string' },
				variant: { type: 'string' }
			}
		}
	},
	/**
	 * An object that defines the alert message at the top.
	 * The keys of object are properties for `now-button`.
	 * @type {{ message: string, type: ('warning'|'info'|'positive')}[]}
	 */
	alertMessage: {
		schema: {
			type: 'object',
			properties: {
				type: { type: 'string', enum: ['warning', 'info', 'positive'] },
				message: { type: 'string' }
			}
		}
	},
	/**
	 * An array of objects that defines the schema for field types.
	 * @type {Object}
	 */
	fields: {
		required: true,
		schema: {
			type: 'array',
			items: {
				type: 'object',
				properties: {
					/**
					 * Enable this property to override automatic updates to the value property.
					 * An action with type SN_LEGAL_MODAL_FIELDSET#MANAGE_VALUE_SET will be dispatched to request updates,
					 * which should be handled by the parent.
					 * @type {boolean}
					 */
					manageValue: {
						type: 'boolean'
					},
					type: { type: 'string', enum: getValidFieldTypes() },
					name: { type: 'string' },
					label: { type: 'string' },
					hidden: { type: 'boolean' },
					required: { type: 'boolean' },
					readonly: { type: 'boolean' },
					disabled: { type: 'boolean' },
					placeholder: { type: 'string' },
					displayValue: { type: 'string' },
					options: { type: 'array' },
					tableName: { type: 'string' },
					fieldName: { type: 'string' },
					tableSysId: { type: 'string' },
					multiSelection: { type: 'boolean' },
					additionalQueryString: { type: 'string' },
					choices: {
						type: 'array',
						items: {
							type: 'object',
							properties: {
								value: { type: 'string' },
								displayValue: { type: 'string' }
							}
						}
					},
					meta: {
						type: 'object'
					}
				}
			}
		}
	},
	uiPolicies: {
		schema: {
			type: 'array',
			items: {
				type: 'object',
				properties: {
					query: { type: 'string' },
					reverseIfFalse: {
						type: 'boolean'
					}
				},
				actions: {
					type: 'array',
					items: {
						type: 'object',
						properties: {
							field: { type: 'string' },
							required: { type: 'string' },
							hidden: { type: 'string' },
							readonly: { type: 'string' }
						}
					}
				}
			}
		}
	}
};
