import { actionTypes } from '@servicenow/ui-core';

import {
	getFieldActionHandlers,
	updateDefaultFields,
	getSubmitPayload,
	enableScriptedModalSubmit
} from './fields/fieldComponentUtils';

import { saveUIPolicyToState } from './uiPolicy/uiPolicyHandler';

export const NOW_ALERT_ACTION_CLICKED = 'NOW_ALERT#ACTION_CLICKED';
export const NOW_MODAL_FOOTER_ACTION_CLICKED =
	'NOW_MODAL#FOOTER_ACTION_CLICKED';
export const SN_SCRIPTED_MODAL_BUTTON_ENABLED_SET =
	'SN_SCRIPTED_MODAL#BUTTON_ENABLED_SET';
export const SN_SCRIPTED_MODAL_DATA_SET = 'SN_SCRIPTED_MODAL#DATA_SET';

export const SN_LEGAL_MODAL_FIELDSET_DATA_SET =
	'SN_LEGAL_MODAL_FIELDSET#DATA_SET';
export const SN_LEGAL_MODAL_FIELDSET_MANAGE_VALUE_SET =
	'SN_LEGAL_MODAL_FIELDSET#MANAGE_VALUE_SET';
export const SN_LEGAL_MODAL_FIELDSET_ALERT_DISMISSED =
	'SN_LEGAL_MODAL_FIELDSET#ALERT_DISMISSED';
export const SN_LEGAL_MODAL_FIELDSET_SUBMIT_CLICKED =
	'SN_LEGAL_MODAL_FIELDSET#SUBMIT_CLICKED';

export const FOOTER_BUTTON_ACTION_SUBMIT = 'submit';
export const FOOTER_BUTTON_ACTION_CANCEL = 'cancel';

export const actions = {
	[SN_LEGAL_MODAL_FIELDSET_DATA_SET]: {
		schema: {
			type: 'object',
			properties: {
				name: { type: 'string' },
				fieldValues: {
					type: 'object',
					patternProperties: {
						'.*': {
							type: 'object',
							properties: {
								oldValue: {},
								newValue: {},
								additionalValues: {}
							}
						}
					},
					errors: { type: 'array' },
					requiredFields: { type: 'object' }
				}
			}
		}
	},
	[SN_LEGAL_MODAL_FIELDSET_MANAGE_VALUE_SET]: {
		schema: {
			type: 'object',
			properties: {
				name: { type: 'string' },
				fieldName: { type: 'string' },
				value: { type: 'string' },
				additionalValues: {}
			}
		}
	},
	[SN_LEGAL_MODAL_FIELDSET_SUBMIT_CLICKED]: {
		schema: {
			type: 'object',
			properties: {
				name: { type: 'string' },
				fieldValues: {
					type: 'object',
					patternProperties: {
						'.*': {}
					},
					errors: { type: 'array' }
				}
			}
		}
	}
};

export const actionHandlers = {
	[actionTypes.COMPONENT_BOOTSTRAPPED]({ host, updateState, dispatch }) {
		updateDefaultFields(updateState, host.fields);
		saveUIPolicyToState(host.uiPolicies, updateState);
		if (host.scriptedModal) {
			enableScriptedModalSubmit(dispatch, false);
		}
	},
	[actionTypes.COMPONENT_PROPERTY_CHANGED]({
		action: {
			payload: { name, value }
		},
		state: { fieldValues },
		updateState
	}) {
		if (name === 'fields') {
			updateDefaultFields(updateState, value, fieldValues);
		}
	},
	[NOW_ALERT_ACTION_CLICKED]: {
		effect({ updateProperties, dispatch }) {
			updateProperties({ showAlert: false });
			dispatch(SN_LEGAL_MODAL_FIELDSET_ALERT_DISMISSED);
		},
		stopPropagation: true
	},
	[NOW_MODAL_FOOTER_ACTION_CLICKED]: {
		effect({ state, action, properties, updateProperties, dispatch }) {
			const buttonAction = action.payload.footerAction.buttonAction;
			if (buttonAction === FOOTER_BUTTON_ACTION_SUBMIT) {
				dispatch(
					SN_LEGAL_MODAL_FIELDSET_SUBMIT_CLICKED,
					getSubmitPayload({
						name: properties.name,
						fieldValues: state.fieldValues,
						errors: []
					})
				);
				if (properties.closeOnSubmit) {
					updateProperties({ opened: false });
				}
			}
			if (buttonAction === FOOTER_BUTTON_ACTION_CANCEL) {
				updateProperties({ opened: false });
			}
		},
		stopPropagation: true
	},
	...getFieldActionHandlers()
};
