import { createHttpEffect } from '@servicenow/ui-effect-http';
import { DROPDOWN_IDS, BUTTON_TYPES, ACTION_TYPES } from './constants';

const getParsedData = response => {
	return response.result.instructions;
};

export const loadInstructionEffect = () =>
	createHttpEffect('/api/now/table/sn_lg_matter_task/:recordId', {
		method: 'GET',
		pathParams: ['recordId'],
		successActionType: ACTION_TYPES.LOAD_INSTRUCTIONS_SUCCEEDED,
		errorActionType: ACTION_TYPES.LOAD_INSTRUCTIONS_FAILED
	});

export const loadInstructionSuccessEffect = ({ state, action, updateState }) =>
	updateState({
		...state,
		createNew: action.payload.result.instructions === null,
		isLoading: false,
		isError: false,
		instructions: getParsedData(action.payload)
	});

export const loadInstructionFailedEffect = ({ state, updateState }) =>
	updateState({
		...state,
		isLoading: false,
		isError: true
	});

export const modalFooterButtonClickedEffect = ({
	state,
	action,
	dispatch,
	updateState
}) => {
	const {
		newInstructions,
		properties: { sysId }
	} = state;
	const buttonAction = action.payload.footerAction.buttonAction;
	if (buttonAction === BUTTON_TYPES.CANCEL) {
		updateState({ createNew: false });
		updateState({ newInstructions: '' });
	}
	if (buttonAction === BUTTON_TYPES.ADD_RECORD) {
		updateState({ isLoading: true });
		dispatch(ACTION_TYPES.UPDATE_TASK_RECORD, {
			sysId,
			data: {
				instructions: newInstructions
			}
		});
	}
};

export const updateTaskRecordEffect = () =>
	createHttpEffect('/api/now/table/sn_lg_matter_task/:sysId', {
		method: 'PUT',
		pathParams: ['sysId'],
		dataParam: 'data',
		successActionType: ACTION_TYPES.TASK_RECORD_UPDATE_SUCCESS,
		errorActionType: ACTION_TYPES.TASK_RECORD_UPDATE_ERROR
	});

export const taskRecordUpdateSuccessEffect = ({ state, updateState }) =>
	updateState({
		...state,
		createNew: false,
		isLoading: false,
		instructions: state.newInstructions,
		newInstructions: ''
	});

export const dropdownItemSelectedEffect = ({
	state,
	action,
	dispatch,
	updateState
}) => {
	const itemId = action.payload.item.id;

	if (itemId === DROPDOWN_IDS.EDIT_INSTRUCTIONS) {
		updateState({ createNew: true });
	} else if (itemId === DROPDOWN_IDS.CLEAR_INSTRUCTIONS) {
		updateState({ isLoading: true });
		dispatch(ACTION_TYPES.UPDATE_TASK_RECORD, {
			sysId: state.properties.sysId,
			data: {
				instructions: ''
			}
		});
	}
};
export const modalCloseEffect = ({ updateState }) => {
	updateState({ createNew: false });
};
