import '@servicenow/now-input';
import '@servicenow/now-textarea';
import '@servicenow/now-checkbox';
import '@servicenow/now-radio-group';
import '@servicenow/now-record-html-editor';
import '@servicenow/now-record-date-picker';
import '@servicenow/now-record-field-lookup-connected';
import '@devsnc/sn-record-file-attachment';
import '@devsnc/sn-form-controls';
import '@devsnc/sn-glide-form-controls';
import '@devsnc/sn-declarative-actions';

import { isValidFieldType } from './fields/fieldTypeUtils';
import { buildComponent } from './fields/fieldComponentUtils';

export const renderFields = fields => {
	return fields.map(field =>
		isValidFieldType(field.type) ? buildComponent(field) : null
	);
};
