import '@servicenow/now-modal';
import '@servicenow/now-textarea';
import { BUTTON_TYPES, LABELS, MODAL_MESSAGES } from './constants';

const getFooterButtons = () => {
	return [
		{
			variant: 'primary',
			label: LABELS.OK,
			buttonAction: BUTTON_TYPES.OK
		},
		{
			variant: 'secondary',
			label: LABELS.CANCEL_BUTTON,
			buttonAction: BUTTON_TYPES.CANCEL
		}
	];
};

const view = state => {
	const { comments, modalOpened } = state;
	return (
		<now-modal
			opened={modalOpened}
			footer-actions={getFooterButtons()}
			header-label={MODAL_MESSAGES.CANCEL_HEADER}
			size="md">
			<div className="legal-related-list-cancel-task--content">
				<now-textarea
					label={MODAL_MESSAGES.CANCEL_MESSAGE}
					className="legal-related-list-cancel-task--textarea"
					autofocus={true}
					autoresize={true}
					value={comments}
				/>
			</div>
		</now-modal>
	);
};

export default view;
