import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	getAPIPath,
	sendErrorNotification,
	refreshList
} from '../../../../utils';
import { closeModal, showLoader, hideLoader } from '../../helperActions';
import {
	SCRIPT_NAMES,
	METHOD_NAMES,
	BASE_API_CONTRACTS
} from '../../../../constants';
import { MESSAGE_UPLOAD_REVISION_FAILED } from './constants';
import {
	UPLOAD_REVISION,
	UPLOAD_REVISION_FAILED,
	UPLOAD_REVISION_SUCCESS
} from './actions';

export const uploadRevisionBootstrapEffect = ({
	updateState,
	properties: { parentRecordSysId }
}) => {
	updateState({
		path: 'uploadRevision.fields[0].tableSysId',
		operation: 'set',
		value: parentRecordSysId
	});
};

export const uploadRevisionSubmitEffect = ({
	dispatch,
	updateState,
	action: {
		payload: { fieldValues }
	},
	properties: { parentRecordSysId, parentTable, sysIds }
}) => {
	showLoader(updateState);
	dispatch(UPLOAD_REVISION, {
		data: {
			parentRecordSysId,
			parentTable,
			sysIds,
			fieldValues
		}
	});
};

export const uploadRevision = () => {
	return createHttpEffect(
		getAPIPath(
			SCRIPT_NAMES.LEGAL_CONTRACT_SERVICE,
			METHOD_NAMES.CREATE_REVISION,
			BASE_API_CONTRACTS
		),
		{
			method: 'POST',
			dataParam: 'data',
			successActionType: UPLOAD_REVISION_SUCCESS,
			errorActionType: UPLOAD_REVISION_FAILED
		}
	);
};

export const uploadRevisionSuccessEffect = ({
	action,
	dispatch,
	updateState
}) => {
	hideLoader(updateState);
	closeModal(updateState);
	const actionSuccess = action.payload.result.success.actionSuccess;
	if (!actionSuccess) {
		var errorMessageRes = action.payload.result.success.errorMessage;
		sendErrorNotification(
			dispatch,
			errorMessageRes || MESSAGE_UPLOAD_REVISION_FAILED
		);
	}
	refreshList(dispatch);
};

export const uploadRevisionFailureEffect = ({ dispatch, updateState }) => {
	hideLoader(updateState);
	closeModal(updateState);
	sendErrorNotification(dispatch, MESSAGE_UPLOAD_REVISION_FAILED);
};
