import '@servicenow/now-modal';
import '@servicenow/now-loader';
import isEmpty from 'lodash/isEmpty';

import { renderFields } from './modalContent';
import { renderAlertMessage } from './alertMessage';
import {
	FOOTER_BUTTON_ACTION_SUBMIT,
	FOOTER_BUTTON_ACTION_CANCEL
} from './actions';
import {
	enableScriptedModalSubmit,
	setScriptedModalData
} from './fields/fieldComponentUtils';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

export const view = (state, { dispatch }) => {
	const {
		fieldValues,
		managedFieldValues,
		allowSubmit,
		properties: {
			fields,
			name,
			size,
			opened,
			scriptedModal,
			showAlert,
			loading,
			headerLabel,
			primaryAction,
			secondaryAction,
			alertMessage
		}
	} = state;
	const showFields = () =>
		!isEmpty(fields) && !isEmpty(fieldValues) ? renderFields(fields) : null;
	const showAlerts = () => renderAlertMessage(showAlert, alertMessage);

	const fieldSetBody = () => (
		<Fragment>
			{showAlerts()}
			{showFields()}
		</Fragment>
	);

	if (scriptedModal) {
		enableScriptedModalSubmit(dispatch, allowSubmit);
		setScriptedModalData(dispatch, {
			name,
			fieldValues: { ...fieldValues, ...managedFieldValues },
			errors: []
		});
	}

	const renderModal = () =>
		scriptedModal ? (
			fieldSetBody()
		) : (
			<now-modal
				opened={opened}
				size={size}
				header-label={headerLabel}
				footer-actions={[
					{
						...primaryAction,
						buttonAction: FOOTER_BUTTON_ACTION_SUBMIT,
						disabled: !allowSubmit
					},
					{ ...secondaryAction, buttonAction: FOOTER_BUTTON_ACTION_CANCEL }
				]}>
				<div className="fieldset-modal-body">
					{loading ? (
						<now-loader label="Loading..." size="lg" />
					) : (
						fieldSetBody()
					)}
				</div>
			</now-modal>
		);

	return renderModal();
};
