import isEmpty from 'lodash/isEmpty';

import { getUpdatedFieldConfig } from './fields/fieldComponentUtils';
import { checkRequiredFieldsAreFilled } from './fields/fieldTypeUtils';
import {
	getUIPolicyTransformedFields,
	applyUIPolicyToFields
} from './uiPolicy/uiPolicyHandler';

export function transformState(state) {
	const {
		fieldValues,
		uiPolicies,
		properties: { fields, manageSubmit, primaryAction }
	} = state;

	let {
		updatedFields,
		updatedFieldValues,
		managedFieldValues
	} = getUpdatedFieldConfig(fields, fieldValues);
	if (!isEmpty(uiPolicies)) {
		const fieldToActionMap = applyUIPolicyToFields(
			uiPolicies,
			updatedFieldValues
		);
		updatedFields = getUIPolicyTransformedFields(
			updatedFields,
			fieldToActionMap
		);
	}

	const allowSubmit = manageSubmit
		? !primaryAction.disabled
		: checkRequiredFieldsAreFilled(updatedFields);
	return {
		...state,
		managedFieldValues,
		allowSubmit,
		properties: {
			...state.properties,
			fields: updatedFields
		}
	};
}
