import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import { preUserData } from 'sn-uxpage-presource';
import { ELEMENTS } from '../../constants';

/**
 * Landing page header components outputs "Hello, First Name" of the current user
 * in workspace.
 *
 * ```jsx
 * <sn-legal-landing-page-header />
 * ```
 *
 * @seismicElement sn-legal-landing-page-header
 * @summary Landing page header component for workspace.
 */
createCustomElement(ELEMENTS.SN_LEGAL_LANDING_PAGE_HEADER, {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [preUserData]
});
