import isEmpty from 'lodash/isEmpty';

import {
	getComponentName,
	getCommonProps,
	getTypeSpecificProps,
	getMetaProps,
	getFieldActionNames,
	getFieldActionProps,
	getDefaultFieldValues,
	isFieldHidden
} from './fieldTypeUtils';
import {
	SN_LEGAL_MODAL_FIELDSET_DATA_SET,
	SN_SCRIPTED_MODAL_DATA_SET,
	SN_SCRIPTED_MODAL_BUTTON_ENABLED_SET,
	SN_LEGAL_MODAL_FIELDSET_MANAGE_VALUE_SET
} from '../actions';

export function updateFieldValues(
	updateState,
	{ fieldName, oldValue, newValue, additionalValues }
) {
	updateState({
		path: `fieldValues.${fieldName}.oldValue`,
		value: oldValue,
		operation: 'set'
	});
	updateState({
		path: `fieldValues.${fieldName}.newValue`,
		value: newValue,
		operation: 'set'
	});

	updateState({
		path: `fieldValues.${fieldName}.additionalValues`,
		value: additionalValues,
		operation: 'set'
	});
}

export function getFinalFieldValues(fieldValues) {
	return Object.entries(fieldValues).reduce((obj, [name, { newValue }]) => {
		obj[name] = newValue;
		return obj;
	}, {});
}

export function findField(fields, fieldName) {
	return fields.find(field => field.name === fieldName);
}

export function getFieldActionHandlers() {
	return getFieldActionNames().reduce((actionHandler, actionName) => {
		actionHandler[actionName] = {
			effect({
				dispatch,
				action: { type, payload },
				state,
				properties: { fields },
				updateState
			}) {
				const {
					actionNameKey,
					actionValueKey,
					additionalProps,
					fieldType
				} = getFieldActionProps(type);
				const fieldName = payload[actionNameKey] || fieldType;
				const field = findField(fields, fieldName);
				const oldValue =
					(state.fieldValues[fieldName] &&
						state.fieldValues[fieldName].newValue) ||
					'';
				const newValue = payload[actionValueKey] || '';
				const additionalValues = additionalProps.reduce(
					(obj, [propKey, payloadKey]) => {
						obj[propKey] = payload[payloadKey];
						return obj;
					},
					{}
				);

				if (field.manageValue) {
					dispatch(SN_LEGAL_MODAL_FIELDSET_MANAGE_VALUE_SET, {
						name: state.properties.name,
						fieldName: field.name,
						value: newValue,
						additionalValues
					});
					return;
				}

				updateFieldValues(updateState, {
					fieldName,
					oldValue,
					newValue,
					additionalValues
				});

				const response = {
					name: state.properties.name,
					changedField: fieldName,
					fieldValues: {
						...state.fieldValues,
						[fieldName]: {
							oldValue,
							newValue,
							additionalValues
						}
					},
					errors: []
				};

				dispatch(SN_LEGAL_MODAL_FIELDSET_DATA_SET, response);
			},
			stopPropagation: true
		};
		return actionHandler;
	}, {});
}

export function getSubmitPayload({ name, fieldValues, errors }) {
	return {
		name,
		fieldValues: getFinalFieldValues(fieldValues),
		errors
	};
}

export function allowSubmit(requiredFields = {}, errors = []) {
	return isEmpty(requiredFields) && isEmpty(errors);
}

export function enableScriptedModalSubmit(dispatch, enable) {
	dispatch(SN_SCRIPTED_MODAL_BUTTON_ENABLED_SET, enable);
}

export function setScriptedModalData(dispatch, data) {
	dispatch(SN_SCRIPTED_MODAL_DATA_SET, getSubmitPayload(data));
}

export function updateDefaultFields(updateState, fields, fieldValues) {
	const defaultFieldValues = getDefaultFieldValues(fields, fieldValues);

	updateState({
		fieldValues: defaultFieldValues
	});
}

export function buildComponentProps(fieldConfig) {
	return {
		...getCommonProps(fieldConfig),
		...getTypeSpecificProps(fieldConfig),
		...getMetaProps(fieldConfig)
	};
}

export function getAdditionalValues(name, fieldValues = {}) {
	return fieldValues[name].additionalValues
		? { ...fieldValues[name].additionalValues }
		: {};
}

export function getComponentValue(name, fieldValues) {
	return {
		value: fieldValues[name].newValue || ''
	};
}

export function getUpdatedFieldConfig(fields, fieldValues = {}) {
	let updatedFieldValues = {};
	let updatedFields = [];
	let managedFieldValues = {};
	fields.forEach(field => {
		let newField = field;
		if (fieldValues.hasOwnProperty(field.name) && !field.manageValue) {
			newField = {
				...field,
				...getComponentValue(field.name, fieldValues),
				...getAdditionalValues(field.name, fieldValues)
			};
		}

		if (field.manageValue) {
			managedFieldValues[field.name] = { newValue: newField.value };
		}

		updatedFieldValues[field.name] = newField.value;
		updatedFields.push(newField);
	});
	return { updatedFields, updatedFieldValues, managedFieldValues };
}

export function buildComponent(config) {
	// eslint-disable-next-line no-unused-vars
	const ComponentName = getComponentName(config.type);
	let componentProps = buildComponentProps(config);
	return (
		<div
			className="fieldset-modal-field"
			style={{ display: isFieldHidden(config) ? 'none' : 'flex' }}>
			<ComponentName {...componentProps} />
		</div>
	);
}
