import { createHttpEffect } from '@servicenow/ui-effect-http';
import { ACTION_TYPES, ORDER_BUFFER } from './constants';
import { getAPIPath } from '../../utils';
import { findTabbableNodes } from '@servicenow/behavior-key-binding';
import get from 'lodash/get';
import set from 'lodash/set';
import { delay } from '../../utils';
import { SCRIPT_NAMES, METHOD_NAMES } from '../../constants';

export const componentBootstrappedEffect = ({
	state,
	action: {
		payload: { host }
	},
	dispatch,
	updateState
}) => {
	updateState({ hostEl: host });
	const checklistId = get(state, 'properties.checklistId');
	const documentId = get(state, 'properties.sysId');

	if (!checklistId && !documentId) {
		// eslint-disable-next-line no-console
		console.error('documentId or checklistId is required');
		updateState({ isLoading: false });
		return;
	}

	if (checklistId) {
		dispatch(ACTION_TYPES.LOAD_WITH_CHECKLIST_ID, { checklistId });
	} else if (documentId) {
		dispatch(ACTION_TYPES.LOAD_WITH_DOCUMENT_ID, { documentId });
	}
};

export const loadWithChecklistIdEffect = () =>
	createHttpEffect('/api/now/checklist/:checklistId', {
		method: 'GET',
		pathParams: ['checklistId'],
		successActionType: ACTION_TYPES.LOAD_SUCCEEDED,
		errorActionType: ACTION_TYPES.LOAD_FAILED
	});

export const loadWithDocumentIdEffect = () =>
	createHttpEffect(
		'/api/now/table/checklist?sysparm_query=document%3D:documentId',
		{
			method: 'GET',
			pathParams: ['documentId'],
			successActionType: ACTION_TYPES.CHECKLIST_ID_RECEIVED,
			errorActionType: ACTION_TYPES.LOAD_FAILED
		}
	);

export const createNewChecklistEffect = () =>
	createHttpEffect('api/now/checklist', {
		method: 'POST',
		dataParams: ['table', 'document'],
		successActionType: ACTION_TYPES.CREATE_CHECKLIST_SUCCESS,
		errorActionType: ACTION_TYPES.LOAD_FAILED
	});

export const createChecklistSuccessEffect = ({
	state,
	action,
	updateState,
	updateProperties
}) => {
	updateState({
		...state,
		isLoading: false,
		createNew: true
	});
	updateProperties({ checklistId: action.payload.result.id });
};

export const updateChecklistIdEffect = ({
	action,
	dispatch,
	updateState,
	updateProperties
}) => {
	const checklistId = get(action, 'payload.result[0].sys_id');

	if (!checklistId) {
		updateState({ isLoading: false });
		return;
	}

	updateProperties({ checklistId });
	dispatch(ACTION_TYPES.LOAD_WITH_CHECKLIST_ID, { checklistId });
};

export const loadSucceededEffect = ({
	state,
	action,
	updateState,
	updateProperties
}) => {
	const items = action.payload.result.items;
	const lastItemIndex = items.length > 0 ? items[items.length - 1].order : 0;
	updateState({
		...state,
		items,
		isLoading: false,
		lastItemOrder: lastItemIndex
	});
	updateProperties({ sysId: action.payload.result.document });
};

export const checkUserHasWriteAccessInterceptor = context => {
	const {
		coeffects: { dispatch, state }
	} = context;
	dispatch(ACTION_TYPES.CHECK_USER_HAS_WRITE_ACCESS, {
		sysId: state.properties.sysId,
		tableName: state.properties.table
	});
	return context;
};

export const checkUserHasWriteAccessEffect = () =>
	createHttpEffect(
		getAPIPath(SCRIPT_NAMES.LEGAL_COMMONS, METHOD_NAMES.CAN_WRITE_RECORD),
		{
			queryParams: ['sysId', 'tableName'],
			successActionType: ACTION_TYPES.CHECK_USER_HAS_WRITE_ACCESS_SUCCESS,
			errorActionType: ACTION_TYPES.LOAD_FAILED
		}
	);

export const checkUserHasWriteAccessSuccessEffect = ({
	state,
	action,
	updateState,
	updateProperties
}) => {
	updateState({
		...state,
		isLoading: false
	});
	updateProperties({ readOnly: !action.payload.result.canWrite });
};

export const loadFailedEffect = ({ state, updateState }) =>
	updateState({
		...state,
		isLoading: false,
		isError: true
	});

export const updateEffectInterceptor = context => {
	const completed = get(context, 'coeffects.action.payload.completed');
	const name = get(context, 'coeffects.action.payload.value');
	if (typeof completed !== 'undefined') {
		set(context, 'coeffects.action.payload.data.complete', completed);
	}
	if (typeof name !== 'undefined') {
		set(context, 'coeffects.action.payload.data.name', name);
	}
	return context;
};

export const updateEffect = () =>
	createHttpEffect('/api/now/checklist/:checklistId/checklistItems/:id', {
		method: 'PUT',
		pathParams: ['id', 'checklistId'],
		errorActionType: ACTION_TYPES.UPDATE_FAILED,
		successActionType: ACTION_TYPES.UPDATE_SUCCEEDED
	});

export const updateSucceededEffect = ({ state, action, updateState }) => {
	updateState({
		...state,
		items: state.items.map(item =>
			item.id === action.payload.result.id
				? {
						...item,
						...action.payload.result
				  }
				: item
		)
	});
};

export const checkListItemAddEffect = () =>
	createHttpEffect('/api/now/checklist/:checklistId/checklistItems', {
		method: 'POST',
		pathParams: ['checklistId'],
		dataParams: ['data'],
		errorActionType: ACTION_TYPES.CHECKLIST_ITEM_ADD_FAILED,
		successActionType: ACTION_TYPES.CHECKLIST_ITEM_ADD_SUCCEEDED
	});

export const checkListItemAddSuccessEffect = ({ state, action, updateState }) =>
	updateState({
		...state,
		items: state.items.concat(action.payload.result),
		lastItemOrder: state.lastItemOrder + ORDER_BUFFER,
		isItemLoading: false,
		value: ''
	});

export const checkListItemRemoveEffect = () =>
	createHttpEffect(
		'/api/now/checklist/:checklistId/checklistItems/:checklistItemId',
		{
			method: 'DELETE',
			pathParams: ['checklistId', 'checklistItemId'],
			errorActionType: ACTION_TYPES.CHECKLIST_ITEM_REMOVE_FAILED,
			successActionType: ACTION_TYPES.CHECKLIST_ITEM_REMOVE_SUCCEEDED
		}
	);

export const checkListItemRemoveSuccessEffect = ({
	state,
	action,
	updateState
}) =>
	updateState({
		...state,
		lastItemOrder:
			state.lastItemOrder - ORDER_BUFFER < 0
				? 0
				: state.lastItemOrder - ORDER_BUFFER,
		items: state.items.filter(item => item.id !== action.payload.result)
	});

export const shiftFocusEffect = ({ state, action }) => {
	delay(0).then(() => {
		const { hostEl, items } = state;
		const focusableElements = findTabbableNodes(hostEl);
		const addButtonEl = focusableElements[focusableElements.length - 1];
		const tabbableElements = [
			...focusableElements.filter(({ tagName }) => tagName === 'INPUT'),
			addButtonEl
		];
		const activeElementId = action.payload.id;
		const activeElIndex = items.findIndex(item => item.id === activeElementId);
		tabbableElements[activeElIndex + 1].focus();
	});
};

export const inputValueSetEffect = ({
	state: { lastItemOrder },
	properties: { checklistId },
	dispatch,
	action: {
		payload: { value, name }
	},
	updateState
}) => {
	if (name === 'create-new') {
		addItem({ lastItemOrder, checklistId, dispatch, updateState, value });
	}
};

export const inputValueInvalidSetEffect = ({ action, updateState }) => {
	if (action.payload.name === 'create-new') {
		updateState({ createNew: false });
	}
};

export const inputValueChangeEffect = ({ action, updateState }) => {
	if (action.payload.name === 'create-new') {
		updateState({ value: action.payload.fieldValue });
	}
};

export const addItem = ({
	lastItemOrder,
	checklistId,
	dispatch,
	updateState,
	value
}) => {
	const inputValue = value.trim();
	if (inputValue) {
		updateState({ isItemLoading: true });
		dispatch(ACTION_TYPES.CHECKLIST_ITEM_ADD, {
			checklistId,
			data: { name: inputValue, order: lastItemOrder + ORDER_BUFFER }
		});
	} else {
		updateState({ createNew: false });
	}
};
