import '../sn-legal-checklist';
import '../sn-legal-instructions';
import { LABELS, ELEMENT_IDS } from './constants';

const view = state => {
	const {
		properties: { sysId, position }
	} = state;
	return (
		<div id={ELEMENT_IDS.TASK_DETAILS} style={{ position }}>
			<div id={ELEMENT_IDS.TASK_INSTRUCTIONS}>
				<sn-legal-instructions
					read-only={`@legal_task_details_checklist_${sysId}/readOnly`}
					instructionTitle={LABELS.INSTRUCTIONS}
					{...state.properties}
				/>
			</div>
			<span id={ELEMENT_IDS.TASK_RESIZER} />
			<div id={ELEMENT_IDS.TASK_CHECKLIST}>
				<sn-legal-checklist
					checklistTitle={LABELS.CHECKLIST}
					component-id={`legal_task_details_checklist_${sysId}`}
					{...state.properties}
					position="relative"
				/>
			</div>
		</div>
	);
};

export default view;
