import { createHttpEffect } from '@servicenow/ui-effect-http';
import { getAPIPath, refreshList } from '../../../../utils';
import {
	closeModal,
	showLoader,
	hideLoader,
	showAlert,
	updateFieldsHiddenState,
	updateFieldsReadOnlyState,
	clearFieldsValues,
	getMergedFieldValues,
	disableSubmitButton
} from '../../helperActions';

import { BASE_API, METHOD_NAMES, SCRIPT_NAMES } from '../../../../constants';

import {
	MESSAGE_INITIATE_APPROVAL_FAILED,
	MESSAGE_INITIATE_APPROVAL_ATTACHMENT_SELECT_OR_NOT,
	INITIATE_APPROVAL,
	INITIATE_APPROVAL_SUCCESS,
	INITIATE_APPROVAL_FAILED,
	GET_APPROVAL_STATUS,
	GET_APPROVAL_STATUS_SUCCESS,
	GET_APPROVAL_STATUS_FAILED
} from './constants';

export const initiateApprovalBootstrapEffect = ({
	dispatch,
	state,
	updateState,
	properties: { parentRecordSysId, parentTable, selectedDefault }
}) => {
	//Initialize to blank to clear previous session and Hide attachment dropdown
	clearFieldsValues(updateState, [1, 4, 5, 6]);
	updateFieldsReadOnlyState(updateState, [0, 1], false);
	updateState({
		path: 'initiateApproval.fields[2].hidden',
		operation: 'set',
		value: true
	});

	let updatedState = {};

	let checkRequestApprovalStatus = false;

	if (parentTable.startsWith('sn_lg_ops_')) {
		checkRequestApprovalStatus = true;
		//Set artifact query
		updateState([
			{
				path: 'initiateApproval.fields[2].additionalQueryString',
				operation: 'set',
				value: 'table_name=' + parentTable + '^table_sys_id=' + parentRecordSysId
			},
			{
				path: 'initiateApproval.fields[0].choices',
				operation: 'set',
				value: [
					{ value: 'request', displayValue: 'Request' },
					{ value: 'attachment', displayValue: 'Attachment' }
				]
			},
			{
				path: 'initiateApproval.fields[0].value',
				operation: 'set',
				value: 'request'
			}
		]);
	//} else if (parentTable === 'sn_lg_matter_matter') {
	} else if (parentTable.startsWith('sn_lg_matter_')) {
		updateState([
			//Set Info message for attachment
			{
				path: 'initiateApproval.fields[2].meta',
				operation: 'set',
				value: {
					messages: [
						{
							type: 'info',
							message: MESSAGE_INITIATE_APPROVAL_ATTACHMENT_SELECT_OR_NOT
						}
					]
				}
			}, //Hide Artifact by default, (as matter would be default on "approvalfor" dropdown)
			{
				path: 'initiateApproval.fields[1].hidden',
				operation: 'set',
				value: true
			}
		]);

		//Parameter has value it means it is from artifact page
		if (selectedDefault) {
			//Set the default for Approval For dropdown, which would be just Artifact
			updateState([
				{
					path: 'initiateApproval.fields[0].choices',
					operation: 'set',
					value: [{ value: 'request', displayValue: 'Artifact' }]
				},
				{
					path: 'initiateApproval.fields[0].value',
					operation: 'set',
					value: 'request'
				},
				{
					path: 'initiateApproval.fields[0].displayValue',
					operation: 'set',
					value: 'Artifact'
				}
			]);

			//Read the parameter and set default value display in the artifact dropdown
			let defaultAry = selectedDefault.split(',');

			if (defaultAry.length > 1) {
				updateFieldsReadOnlyState(updateState, [0, 1], true);
				updateFieldsHiddenState(updateState, [1, 2], false);
				updateState([
					{
						path: 'initiateApproval.fields[1].value',
						operation: 'set',
						value: defaultAry[0]
					},
					{
						path: 'initiateApproval.fields[1].displayValue',
						operation: 'set',
						value: defaultAry[1]
					},
					{
						path: 'initiateApproval.fields[2].additionalQueryString',
						operation: 'set',
						value:
							'table_name=sn_lg_matter_artifact^table_sys_id=' + defaultAry[0]
					}
				]);

				updatedState[state.initiateApproval.fields[1].name] = {
					newValue: defaultAry[0]
				};
			}
		} //Else case is it is from Matter screen home page or related list
		else {
			checkRequestApprovalStatus = true;

			//Set artifact drop down
			updateState([
				{
					path: 'initiateApproval.fields[1].additionalQueryString',
					operation: 'set',
					value: 'parent_matter=' + parentRecordSysId
				},
				//Set Approval For dropdown to have only Matter and Artifact
				{
					path: 'initiateApproval.fields[0].choices',
					operation: 'set',
					value: [
						{ value: 'request', displayValue: 'Matter' },
						{ value: 'artifact', displayValue: 'Artifact' }
					]
				},
				{
					path: 'initiateApproval.fields[0].value',
					operation: 'set',
					value: 'request'
				}
			]);
		}
	}

	let fieldValuesMerged = getMergedFieldValues(state, updatedState);

	dispatch(GET_APPROVAL_STATUS, {
		data: {
			parentRecordSysId,
			parentTable,
			selectedDefault,
			fieldName: checkRequestApprovalStatus ? 'approval_for' : 'artifact',
			fieldValues: fieldValuesMerged
		}
	});
};

export const initiateApprovalSubmitEffect = ({
	state,
	dispatch,
	updateState,
	properties: { parentRecordSysId, parentTable, selectedDefault }
}) => {
	showLoader(updateState);

	let fieldValuesMerged = getMergedFieldValues(state);

	dispatch(INITIATE_APPROVAL, {
		data: {
			parentRecordSysId,
			parentTable,
			selectedDefault,
			fieldValues: fieldValuesMerged
		}
	});
};

export const initiateApproval = () => {
	return createHttpEffect(
		getAPIPath(
			SCRIPT_NAMES.LEGAL_ADHOC_APPROVAL,
			METHOD_NAMES.INITIATE_APPROVAL_REQUEST,
			BASE_API
		),
		{
			method: 'POST',
			dataParam: 'data',
			successActionType: INITIATE_APPROVAL_SUCCESS,
			errorActionType: INITIATE_APPROVAL_FAILED
		}
	);
};

export const initiateApprovalSuccessEffect = ({
	action,
	dispatch,
	updateState
}) => {
	hideLoader(updateState);

	if (action.payload.result && action.payload.result.success) {
		const actionSuccess = action.payload.result.success.actionSuccess;
		if (!actionSuccess) {
			let errorMessageRes = action.payload.result.success.errorMessage;

			showAlert(updateState, 'initiateApproval', 'warning', errorMessageRes);
		} else {
			closeModal(updateState);
			refreshList(dispatch);
		}
	}
};

export const initiateApprovalFailureEffect = ({ updateState }) => {
	hideLoader(updateState);
	closeModal(updateState);
	showAlert(
		updateState,
		'initiateApproval',
		'warning',
		MESSAGE_INITIATE_APPROVAL_FAILED
	);
};

export const getApprovalStatus = () => {
	return createHttpEffect(
		getAPIPath(
			SCRIPT_NAMES.LEGAL_ADHOC_APPROVAL,
			METHOD_NAMES.GET_APPROVAL_STATUS,
			BASE_API
		),
		{
			method: 'post',
			dataParam: 'data',
			successActionType: GET_APPROVAL_STATUS_SUCCESS,
			errorActionType: GET_APPROVAL_STATUS_FAILED
		}
	);
};

export const getApprovalStatusSuccessEffect = ({
	action,
	updateState,
	properties: { selectedDefault }
}) => {
	let curFieldName = '';

	if (action.meta.request.data.fieldName)
		curFieldName = action.meta.request.data.fieldName;

	let attachmentMessage;
	const actionSuccess = action.payload.result.success.actionSuccess;

	if (curFieldName === 'attachment' || curFieldName === 'artifact') {
		if (!actionSuccess) {
			attachmentMessage = {
				messages: [
					{
						type: curFieldName === 'attachment' ? 'error' : 'info',
						message: action.payload.result.success.errorMessage
					}
				]
			};

			if (curFieldName === 'attachment') {
				disableSubmitButton(updateState, true);
			}
		} else {
			attachmentMessage = null;
			disableSubmitButton(updateState, false);
		}

		if (curFieldName === 'attachment') {
			updateState({
				path: 'initiateApproval.fields[2].meta',
				operation: 'set',
				value: attachmentMessage
			});
		} else if (curFieldName === 'artifact') {
			updateState({
				path: 'initiateApproval.fields[1].meta',
				operation: 'set',
				value: attachmentMessage
			});

			//New Change
			if (!selectedDefault) {
				updateState([
					{
						path: 'initiateApproval.fields[1].meta',
						operation: 'set',
						value: attachmentMessage
					},
					{
						path: 'initiateApproval.fields[2].additionalQueryString',
						operation: 'set',
						value:
							'table_name=sn_lg_matter_artifact^table_sys_id=' +
							action.meta.request.data.fieldValues.artifact
					}
				]);
			}

			if (attachmentMessage === null) {
				updateState([
					{
						path: 'initiateApproval.fields[2].hidden',
						operation: 'set',
						value: false
					},
					{
						path: 'initiateApproval.fields[2].meta',
						operation: 'set',
						value: {
							messages: [
								{
									type: 'info',
									message: MESSAGE_INITIATE_APPROVAL_ATTACHMENT_SELECT_OR_NOT
								}
							]
						}
					}
				]);
			}
		}
	} else if (curFieldName === 'approval_for') {
		if (!actionSuccess) {
			updateState({
				path: 'initiateApproval.fields[0].meta',
				operation: 'set',
				value: {
					messages: [
						{
							type: 'error',
							message: action.payload.result.success.errorMessage
						}
					]
				}
			});

			disableSubmitButton(updateState, true);
		} else {
			updateState({
				path: 'initiateApproval.fields[0].meta',
				operation: 'set',
				value: { messages: [] }
			});
		}
	}
};

export const getApprovalStatusFailureEffect = ({ updateState }) => {
	showAlert(
		updateState,
		'initiateApproval',
		'warning',
		MESSAGE_INITIATE_APPROVAL_FAILED
	);
};

export const initiateApprovalOnChangeValueEffect = ({
	action,
	properties: { parentRecordSysId, parentTable, selectedDefault },
	dispatch,
	updateState,
	state
} = response) => {
	let objChanged = action.payload.fieldValues;

	let updatedState = {};

	if (action.payload.changedField) {
		let fieldPosition = state.initiateApproval.fields.findIndex(
			e => e.name === action.payload.changedField
		);

		if (fieldPosition != -1) {
			updateState({
				path: 'initiateApproval.fields[' + fieldPosition + '].value',
				operation: 'set',
				value: objChanged[action.payload.changedField].newValue
			});
		}

		if (action.payload.changedField === 'approval_for') {
			disableSubmitButton(updateState, false);

			if (objChanged[action.payload.changedField].newValue === 'request') {
				//Show attachment dropdown
				updateState({
					path: 'initiateApproval.fields[2].hidden',
					operation: 'set',
					value: true
				});

				updatedState[action.payload.changedField] = {
					newValue: objChanged[action.payload.changedField].newValue
				};

				let fieldValuesMerged = getMergedFieldValues(state, updatedState);

				dispatch(GET_APPROVAL_STATUS, {
					data: {
						parentRecordSysId,
						parentTable,
						selectedDefault,
						fieldName: 'approval_for',
						fieldValues: fieldValuesMerged
					}
				});
			} else {
				updateState({
					path: 'initiateApproval.fields[0].meta',
					operation: 'set',
					value: { messages: [] }
				});

				if (state.initiateApproval.fields[1].value) {
					//Show attachment dropdown
					updateState({
						path: 'initiateApproval.fields[2].hidden',
						operation: 'set',
						value: false
					});
				}
			}
		}
	}
};

export const initiateApprovalOnManageValueEffect = ({
	state,
	updateState,
	dispatch,
	action: {
		payload: { fieldName, value, additionalValues }
	},
	properties: { parentRecordSysId, parentTable, selectedDefault }
}) => {
	let updatedState = {};

	updatedState[fieldName] = { newValue: value };

	if (fieldName === 'attachment') {
		updateState([
			{
				path: 'initiateApproval.fields[2].value',
				operation: 'set',
				value: value
			},
			{
				path: `initiateApproval.fields[2].displayValue`,
				operation: 'set',
				value: additionalValues.displayValue
			}
		]);

		if (value) {
			let fieldValuesMerged = getMergedFieldValues(state, updatedState);

			dispatch(GET_APPROVAL_STATUS, {
				data: {
					parentRecordSysId,
					parentTable,
					selectedDefault,
					fieldName: 'attachment',
					fieldValues: fieldValuesMerged
				}
			});
		} else {
			updateState([
				{
					path: 'initiateApproval.fields[2].meta',
					operation: 'set',
					value: { messages: [] }
				},
				{
					path: `initiateApproval.manageSubmit`,
					value: false,
					operation: 'set'
				}
			]);
		}
	} else if (fieldName === 'artifact') {
		updateState([
			{
				path: 'initiateApproval.fields[1].value',
				operation: 'set',
				value: value
			},
			{
				path: `initiateApproval.fields[1].displayValue`,
				operation: 'set',
				value: additionalValues.displayValue
			},
			{
				path: 'initiateApproval.fields[2].value',
				operation: 'set',
				value: ''
			},
			{
				path: `initiateApproval.fields[2].displayValue`,
				value: '',
				operation: 'set'
			},
			{
				path: 'initiateApproval.fields[2].meta',
				operation: 'set',
				value: null
			},
			{
				path: 'initiateApproval.fields[2].hidden',
				operation: 'set',
				value: 'true'
			}
		]);

		updatedState[state.initiateApproval.fields[2].name] = { newValue: '' };

		if (value) {
			let fieldValuesMerged = getMergedFieldValues(state, updatedState);

			dispatch(GET_APPROVAL_STATUS, {
				data: {
					parentRecordSysId,
					parentTable,
					selectedDefault,
					fieldName: 'artifact',
					fieldValues: fieldValuesMerged
				}
			});
		}
	}
};
