import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import actionHandlers from './actions';
import view from './view';
import styles from './styles.scss';
import { ELEMENTS } from '../../constants';

/**
 * Cancel Multiple Tasks in matter
 * ```jsx
 * <sn-legal-related-list-cancel-task
 *		sys-ids="ee348d570f533300d7f1c06868767e5c,2a348d570f533300d7f1c06868767e5f"
 *		/>
 * ```
 *
 * @seismicElement sn-legal-related-list-cancel-task
 * @summary Cancel Multiple Tasks.
 */

createCustomElement(ELEMENTS.SN_LEGAL_RELATED_LIST_CANCEL_TASK, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/**
		 * Comma separated values of sys ids of matter tasks that needs to be cancelled
		 * @type {string}
		 */
		sysIds: {
			default: ''
		}
	},
	initialState: {
		modalOpened: true,
		comments: ''
	},
	actionHandlers,
	styles
});
