import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import { view } from './view';
import styles from './styles.scss';
import { transformState } from './transformState';
import { actions, actionHandlers } from './actions';
import { properties } from './properties';

/**
 * A fieldset modal is a controlled component which groups a set of input aka
 * form control components by accepting an array of fields as properties
 * and makes it easier to manage them.
 *
 *
 * ```jsx
 * <sn-fieldset-modal
 * size='lg'
 *	name='document_revision'
 *	showAlert='true'
 *	headerLabel='Upload Revision'
 *	primaryAction='{
 *		"variant": "primary",
 *		"label": "Upload"
 *	}'
 *	fields='[
 *	   {
 *		"type": "boolean",
 *		"name": "active",
 *		"label": "Active" ,
 *	  },
 * 	  {
 *		"type":  "attachment",
 *		"name":  "attachment",
 *		"label":  "Attachment",
 *		"required": true
 *	  }
 * 	]'
 *	alertMessage='{
 *		"type": "info",
 *		"message": "File name and version increment will auto update"
 *	}'
 * />
 * ```
 *
 * @seismicElement sn-fieldset-modal
 * @summary A controlled component which makes it easier to manage multiple form control components.
 */
createCustomElement('sn-legal-modal-fieldset', {
	renderer: {
		type: snabbdom,
		view
	},
	transformState,
	properties,
	initialState: {
		errors: [],
		fieldValues: {}
	},
	actions,
	actionHandlers,
	styles
});
