import {
	operatorKeys,
	operatorConfig,
	operatorNames,
	separators
} from './operators';

// https://code.devsnc.com/dev/glide/blob/6ecbcb07846a4da25085b982c287858273dba3ac/glide/plugins/com.glide.list_v2/ui.html/scripts/classes/GlideList2.js#L1614
export function separateIntoTerms(queryString) {
	var terms = [];
	var startIndex = 0;
	while (queryString.substring(startIndex).length > 0) {
		var separatorIndex = queryString.indexOf('^', startIndex);

		if (separatorIndex == -1) {
			terms.push(queryString.substring(startIndex));
			break;
		} else if (separatorIndex == queryString.length - 1) {
			// last character is '^'
			terms.push(queryString.substring(startIndex, separatorIndex));
			break;
		}

		if (separatorIndex < queryString.length - 1) {
			// check for ^^ which means treat the separator character ^ as a literal
			while (
				queryString.charAt(separatorIndex + 1) == '^' &&
				separatorIndex !== -1
			) {
				separatorIndex = queryString.indexOf('^', separatorIndex + 2);
			}

			// no more '^'s...
			if (separatorIndex == -1) {separatorIndex = queryString.length;}

			// add our term... but remove any escaping '^'s
			var term = queryString.substring(startIndex, separatorIndex);
			terms.push(term);

			// let's not walk off the end of our string when we go try to parse the rest of the string
			if (++separatorIndex > queryString.length)
				{separatorIndex = queryString.length;}

			startIndex = separatorIndex;
		}
	}
	return terms;
}

export function splitPredicateAndType(term) {
	if (term.startsWith(separators.OR)) {
		return { type: separators.OR, predicate: term.slice(2) };
	}
	if (term.startsWith(separators.NQ)) {
		return { type: separators.NQ, predicate: term.slice(2) };
	}
	return { type: separators.AND, predicate: term };
}

export function validatePredicateField(fieldMap) {
	return predicateObj => {
		return fieldMap.hasOwnProperty(predicateObj.field);
	};
}

export function getOperatorKey(type) {
	return operatorKeys[type];
}

export function getValueForType(value, type) {
	let operatorKey = getOperatorKey(type);
	return operatorConfig[operatorKey].getActualValue(value);
}

export function buildPredicateObject(term) {
	let { type, predicate } = splitPredicateAndType(term);
	let operatorRegExp = new RegExp(`(${operatorNames.join('|')})`);
	let elements = predicate.split(operatorRegExp);
	let value = getValueForType(elements[2], elements[1]);
	return {
		predicate,
		field: elements[0],
		operator: elements[1],
		value,
		type
	};
}

export function buildPredicateList(queryTerms) {
	return queryTerms.map(buildPredicateObject);
}

export function sanitizePredicateList(predicateList, fieldMap) {
	return predicateList.filter(validatePredicateField(fieldMap));
}

export function buildAndSanitizePredicateList(queryTerms, fieldMap) {
	return sanitizePredicateList(buildPredicateList(queryTerms), fieldMap);
}

export function evaluatePredicate({ operator, value }, fieldValue) {
	let operatorKey = getOperatorKey(operator);
	return operatorConfig[operatorKey].evaluatePredicate(value, fieldValue);
}
