import { actionSchema } from './actionMap';
import get from 'lodash/get';
import transform from 'lodash/transform';
import reduce from 'lodash/reduce';

export function getFieldsetProps(name, state) {
	const path = get(actionSchema, `${name}.state.name`);
	return get(state, path, {});
}

export function getPartialActions() {
	return reduce(
		actionSchema,
		(actions, v) => {
			return { ...actions, ...v.actions };
		},
		{}
	);
}

export function getPartialInitialState() {
	return transform(actionSchema, (state, v) => {
		state[v.state.name] = v.state.value;
	});
}

function executeEffect(type) {
	return function(coeffects) {
		let name = coeffects.action.payload.name || coeffects.properties.action;
		if (
			!actionSchema.hasOwnProperty(name) ||
			!actionSchema[name].effects.hasOwnProperty(type)
		) {
			return;
		}
		let effectHandler = actionSchema[name].effects[type];
		return effectHandler(coeffects);
	};
}

export const fieldSetUpdatedEffect = executeEffect('onChange');
export const fieldSetSubmitEffect = executeEffect('onSubmit');
export const fieldSetBootstrapEffect = executeEffect('onBootstrap');
export const fieldSetManageValueEffect = executeEffect('onManageValue');
