import pick from 'lodash/pick';
import get from 'lodash/get';

import {
	fieldTypesToComponents,
	legacyFieldTypes,
	typeSpecificProps,
	fieldSchema,
	fieldTypesToActionNames,
	actionNamesToFieldTypes,
	commonProps,
	validFieldTypes
} from './fieldMappings';

export function getValidFieldTypes() {
	return validFieldTypes;
}

export function getLegacyFieldTypes() {
	return legacyFieldTypes;
}

export function isValidFieldType(fieldType) {
	return fieldTypesToComponents.hasOwnProperty(fieldType);
}

export function isLegacyFieldType(fieldType) {
	return getLegacyFieldTypes().includes(fieldType);
}

export function getComponentName(fieldType) {
	return isValidFieldType(fieldType) && fieldTypesToComponents[fieldType];
}

export function getFieldActionNames() {
	return Object.values(fieldTypesToActionNames).flat(1);
}

export function getFieldActionName(fieldType) {
	return fieldTypesToActionNames[fieldType];
}

export function getFieldType(actionName) {
	return actionNamesToFieldTypes[actionName];
}

export function getActionNameKey(fieldType) {
	return get(fieldSchema, `${fieldType}.actions[0].nameKey`, 'name');
}

export function getActionValueKey(fieldType) {
	return get(fieldSchema, `${fieldType}.actions[0].valueKey`, 'value');
}

export function getAdditionalPayloadEntries(fieldType) {
	return Object.entries(
		get(fieldSchema, `${fieldType}.actions[0].additionalProps`, {})
	);
}

export function getFieldActionProps(actionName) {
	const fieldType = getFieldType(actionName);
	return {
		fieldType,
		actionNameKey: getActionNameKey(fieldType),
		actionValueKey: getActionValueKey(fieldType),
		additionalProps: getAdditionalPayloadEntries(fieldType)
	};
}

export function getCommonProps(config) {
	return pick(config, commonProps, {});
}

export function isFieldHidden({ hidden = false }) {
	return hidden === true;
}

export function getTypeSpecificProps({ type, ...config }) {
	const propertiesList = typeSpecificProps[type] || [];
	return pick(config, propertiesList);
}

export function getMetaProps(config) {
	const metaProps = get(config, 'meta', {});
	return { ...metaProps };
}

export function getDefaultFieldValues(fieldConfig, fieldValues) {
	return fieldConfig.reduce((fieldValueObj, field) => {
		if (!field.manageValue) {
			fieldValueObj[field.name] =
				fieldValues && fieldValues[field.name]
					? fieldValues[field.name]
					: {
							oldValue: field.value || '',
							newValue: field.value || ''
					  };
		}
		return fieldValueObj;
	}, {});
}

export function checkRequiredFieldsAreFilled(fields) {
	return fields
		.filter(field => field.required && !field.hidden)
		.every(field => field.value !== '');
}
