import { ACTION_TYPES } from './constants';

import {
	getDefaultTemplateEffect,
	getDefaultTemplateSuccessEffect,
	getDefaultTemplateFailedEffect,
	createMatter,
	createMatterSuccessEffect,
	createMatterFailureEffect
} from './effects';

export default {
	[ACTION_TYPES.GET_DEFAULT_TEMPLATE]: {
		...getDefaultTemplateEffect(),
		stopPropagation: true
	},

	[ACTION_TYPES.GET_DEFAULT_TEMPLATE_SUCCESS]: {
		effect: getDefaultTemplateSuccessEffect,
		stopPropagation: true
	},

	[ACTION_TYPES.GET_DEFAULT_TEMPLATE_FAILED]: {
		effect: getDefaultTemplateFailedEffect,
		stopPropagation: true
	},

	[ACTION_TYPES.CREATE_MATTER]: {
		...createMatter(),
		stopPropagation: true
	},
	[ACTION_TYPES.CREATE_MATTER_SUCCESS]: {
		effect: createMatterSuccessEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.CREATE_MATTER_FAILURE]: {
		effect: createMatterFailureEffect,
		stopPropagation: true
	}
};
