import { createHttpEffect } from '@servicenow/ui-effect-http';
import { SCRIPT_NAMES, METHOD_NAMES } from '../../constants';
import { getAPIPath } from '../../utils';
import { BUTTON_TYPES, ACTION_TYPES, CANCEL_TASKS_FAILED } from './constants';

export const modalFooterButtonClickedEffect = ({
	state,
	action,
	dispatch,
	updateState
}) => {
	const {
		comments,
		properties: { sysIds }
	} = state;

	const buttonAction = action.payload.footerAction.buttonAction;

	if (buttonAction === BUTTON_TYPES.CANCEL) {
		updateState({ modalOpened: false });
	}
	if (buttonAction === BUTTON_TYPES.OK) {
		dispatch(ACTION_TYPES.CANCEL_TASK_RECORDS, {
			data: {
				comments,
				sysIds
			}
		});
	}
};

export const cancelTaskRecords = () => {
	return createHttpEffect(
		getAPIPath(SCRIPT_NAMES.LEGAL_COMMONS, METHOD_NAMES.CANCEL_TASKS),
		{
			method: 'POST',
			dataParam: 'data',
			successActionType: ACTION_TYPES.CANCEL_TASK_RECORDS_SUCCESS,
			errorActionType: ACTION_TYPES.CANCEL_TASK_RECORDS_ERROR
		}
	);
};

export const cancelTaskSuccessEffect = ({ action, dispatch, updateState }) => {
	updateState({ modalOpened: false });
	const actionSuccess = action.payload.result.success.actionSuccess;
	let listNotifications = [];
	if (!actionSuccess || actionSuccess == null) {
		const errorMessage = {
			type: 'error',
			message: CANCEL_TASKS_FAILED,
			staticMessage: false
		};
		listNotifications.push(errorMessage);
	}
	if (listNotifications.length > 0) {
		dispatch(ACTION_TYPES.ADD_NOTIFICATIONS, {
			notifications: listNotifications
		});
	}
	dispatch(ACTION_TYPES.REFRESH_REQUESTED, {
		timestamp: Date.now()
	});
};

export const cancelTaskErrorEffect = ({ dispatch, updateState }) => {
	updateState({ modalOpened: false });
	let listNotifications = [];
	const errorMessage = {
		type: 'error',
		message: CANCEL_TASKS_FAILED,
		staticMessage: false
	};
	listNotifications.push(errorMessage);
	if (listNotifications.length > 0) {
		dispatch(ACTION_TYPES.ADD_NOTIFICATIONS, {
			notifications: listNotifications
		});
	}
};
