import { actionTypes } from '@servicenow/ui-core';
import { getElementFromShadowRoot } from '../../utils';
import {
	MIN_BOTTOM_ROW_HEIGHT,
	MIN_TOP_ROW_HEIGHT,
	ELEMENT_IDS
} from './constants';
import { createResizableController } from './resizableController';

let resizableController = null;

export default {
	[actionTypes.COMPONENT_CONNECTED]: {
		effect({
			action: {
				payload: { host }
			}
		}) {
			const containerEl = getElementFromShadowRoot(
				host,
				ELEMENT_IDS.TASK_DETAILS
			);
			const topEl = getElementFromShadowRoot(
				host,
				ELEMENT_IDS.TASK_INSTRUCTIONS
			);
			const resizerEl = getElementFromShadowRoot(
				host,
				ELEMENT_IDS.TASK_RESIZER
			);

			resizableController = createResizableController(
				containerEl,
				topEl,
				resizerEl,
				{},
				{
					minTopRowHeight: MIN_TOP_ROW_HEIGHT,
					minBottomRowHeight: MIN_BOTTOM_ROW_HEIGHT,
					allowKeyAccess: false
				}
			);
			resizableController.initialize();
		}
	},

	[actionTypes.COMPONENT_DISCONNECTED]: {
		effect() {
			resizableController.destroy();
		}
	}
};
