import { ACTION_TYPES } from './constants';
import {
	modalFooterButtonClickedEffect,
	cancelTaskRecords,
	cancelTaskSuccessEffect,
	cancelTaskErrorEffect
} from './effects';
export default {
	[ACTION_TYPES.NOW_MODAL_FOOTER_ACTION_CLICKED]: {
		effect: modalFooterButtonClickedEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.NOW_TEXTAREA_VALUE_SET]: {
		effect({ updateState, action }) {
			updateState({ comments: action.payload.value });
		},
		stopPropagation: true
	},
	[ACTION_TYPES.CANCEL_TASK_RECORDS]: cancelTaskRecords(),
	[ACTION_TYPES.CANCEL_TASK_RECORDS_SUCCESS]: {
		effect: cancelTaskSuccessEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.CANCEL_TASK_RECORDS_ERROR]: {
		effect: cancelTaskErrorEffect,
		stopPropagation: true
	}
};
