import {
	initiateApproval,
	initiateApprovalSuccessEffect,
	initiateApprovalFailureEffect,
	getApprovalStatus,
	getApprovalStatusSuccessEffect,
	getApprovalStatusFailureEffect
} from './effects';

import {
	INITIATE_APPROVAL,
	INITIATE_APPROVAL_SUCCESS,
	INITIATE_APPROVAL_FAILED,
	GET_APPROVAL_STATUS,
	GET_APPROVAL_STATUS_SUCCESS,
	GET_APPROVAL_STATUS_FAILED
} from './constants';

export default {
	[INITIATE_APPROVAL]: {
		...initiateApproval(),
		stopPropagation: true
	},

	[INITIATE_APPROVAL_SUCCESS]: {
		effect: initiateApprovalSuccessEffect,
		stopPropagation: true
	},

	[INITIATE_APPROVAL_FAILED]: {
		effect: initiateApprovalFailureEffect,
		stopPropagation: true
	},

	[GET_APPROVAL_STATUS]: {
		...getApprovalStatus(),
		stopPropagation: true
	},

	[GET_APPROVAL_STATUS_SUCCESS]: {
		effect: getApprovalStatusSuccessEffect,
		stopPropagation: true
	},

	[GET_APPROVAL_STATUS_FAILED]: {
		effect: getApprovalStatusFailureEffect,
		stopPropagation: true
	}
};
