import { ACTION_TYPES } from './constants';
import { actionTypes } from '@servicenow/ui-core';
import {
	loadInstructionEffect,
	loadInstructionSuccessEffect,
	loadInstructionFailedEffect,
	modalFooterButtonClickedEffect,
	updateTaskRecordEffect,
	taskRecordUpdateSuccessEffect,
	dropdownItemSelectedEffect,
	modalCloseEffect
} from './effects';
export default {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: {
		effect({ state, dispatch }) {
			const recordId = state.properties.sysId;
			if (recordId) {
				dispatch(ACTION_TYPES.LOAD_INSTRUCTIONS_WITH_RECORD_ID, {
					recordId
				});
			}
		}
	},
	[ACTION_TYPES.LOAD_INSTRUCTIONS_WITH_RECORD_ID]: loadInstructionEffect(),
	[ACTION_TYPES.LOAD_INSTRUCTIONS_SUCCEEDED]: {
		effect: loadInstructionSuccessEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.LOAD_INSTRUCTIONS_FAILED]: {
		effect: loadInstructionFailedEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.NOW_MODAL_FOOTER_ACTION_CLICKED]: {
		effect: modalFooterButtonClickedEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.UPDATE_TASK_RECORD]: updateTaskRecordEffect(),
	[ACTION_TYPES.TASK_RECORD_UPDATE_SUCCESS]: {
		effect: taskRecordUpdateSuccessEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.NOW_DROPDOWN_ITEM_CLICKED]: {
		effect: dropdownItemSelectedEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.NOW_MODAL_OPENED_SET]: {
		effect: modalCloseEffect,
		stopPropagation: true
	}
};
