import isEmpty from 'lodash/isEmpty';
import pick from 'lodash/pick';

import {
	evaluatePredicate,
	sanitizePredicateList,
	separateIntoTerms,
	buildPredicateList
} from './queryUtils';
import { separators, uiPolicyActions } from './operators';

export function executeUIPolicy(predicateList, fieldMap) {
	let exprStack = [];
	let sanitizedPredList = sanitizePredicateList(predicateList, fieldMap);
	if (isEmpty(sanitizedPredList)) {
		return false;
	}
	sanitizedPredList.forEach(predicate => {
		let result = evaluatePredicate(predicate, fieldMap[predicate.field]);
		if (predicate.type === separators.AND) {
			exprStack.push(result);
		} else if (predicate.type === separators.OR) {
			let previousResult = exprStack.pop();
			let newResult = result || previousResult;
			exprStack.push(newResult);
		}
	});

	return exprStack.reduce((accum, current) => {
		return accum && current;
	});
}

export function saveUIPolicyToState(uiPolicies = [], updateState) {
	let policyObjectList = uiPolicies.map(({ query, actions }) => {
		let queryTerms = separateIntoTerms(query || '');
		let predicateList = buildPredicateList(queryTerms);
		return { predicateList, actions };
	});

	updateState({
		path: 'uiPolicies',
		value: policyObjectList,
		operation: 'assign',
		shouldRender: false
	});
}

export function applyUIPolicyToFields(uiPolicies, fieldMap) {
	return uiPolicies.reduce((fieldActionMap, uiPolicy) => {
		let result = executeUIPolicy(uiPolicy.predicateList, fieldMap);
		if (result) {
			uiPolicy.actions.forEach(action => {
				fieldActionMap[action.field] = {
					...fieldActionMap[action.field],
					...pick(action, uiPolicyActions)
				};
			});
		}
		return fieldActionMap;
	}, {});
}

export function getUIPolicyTransformedFields(fields, fieldMap) {
	return fields.map(field => {
		if (fieldMap.hasOwnProperty(field.name)) {
			return { ...field, ...fieldMap[field.name] };
		}
		return field;
	});
}
