import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';

import '@servicenow/now-heading';

const view = state => {
	const {
		properties: { name }
	} = state;
	const label = t('Hello, {0}', name);

	return (
		<now-heading
			className="heading"
			level="1"
			variant="header-primary"
			wontWrap={true}
			label={label}
		/>
	);
};

createCustomElement('sn-landing-page-header', {
	renderer: {
		type: snabbdom,
		view
	},

	properties: {
		/**
		 * The user name to include in the message
		 *
		 * @type {String}
		 */
		name: {
			reflect: true
		}
	}
});
