import {
	componentBootstrappedEffect,
	loadWithChecklistIdEffect,
	loadWithDocumentIdEffect,
	updateChecklistIdEffect,
	loadSucceededEffect,
	loadFailedEffect,
	shiftFocusEffect,
	createNewChecklistEffect,
	createChecklistSuccessEffect,
	checkUserHasWriteAccessInterceptor,
	checkUserHasWriteAccessEffect,
	checkUserHasWriteAccessSuccessEffect,
	updateEffectInterceptor,
	updateEffect,
	updateSucceededEffect,
	checkListItemAddEffect,
	checkListItemAddSuccessEffect,
	checkListItemRemoveEffect,
	checkListItemRemoveSuccessEffect,
	inputValueSetEffect,
	inputValueInvalidSetEffect,
	inputValueChangeEffect
} from './effects';

import { actionTypes } from '@servicenow/ui-core';
import { logErrorEffect, effectWithInterceptors } from '../../utils';

import { ACTION_TYPES } from './constants';

export default {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: {
		effect: componentBootstrappedEffect
	},
	[ACTION_TYPES.LOAD_WITH_CHECKLIST_ID]: loadWithChecklistIdEffect(),
	[ACTION_TYPES.LOAD_WITH_DOCUMENT_ID]: effectWithInterceptors(
		loadWithDocumentIdEffect(),
		[{ after: checkUserHasWriteAccessInterceptor }]
	),
	[ACTION_TYPES.CHECKLIST_ID_RECEIVED]: { effect: updateChecklistIdEffect },
	[ACTION_TYPES.LOAD_SUCCEEDED]: {
		effect: loadSucceededEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.LOAD_FAILED]: {
		effect: loadFailedEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.CREATE_NEW_CHECKLIST]: createNewChecklistEffect(),
	[ACTION_TYPES.CREATE_CHECKLIST_SUCCESS]: {
		effect: createChecklistSuccessEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.CREATE_CHECKLIST_FAILED]: {
		effect: loadFailedEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.UPDATE]: effectWithInterceptors(updateEffect(), [
		updateEffectInterceptor
	]),
	[ACTION_TYPES.SHIFT_FOCUS]: { effect: shiftFocusEffect },
	[ACTION_TYPES.CHECK_USER_HAS_WRITE_ACCESS]: checkUserHasWriteAccessEffect(),
	[ACTION_TYPES.CHECK_USER_HAS_WRITE_ACCESS_SUCCESS]: {
		effect: checkUserHasWriteAccessSuccessEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.CHECK_USER_HAS_WRITE_ACCESS_FAILED]: {
		effect: loadFailedEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.UPDATE_SUCCEEDED]: {
		effect: updateSucceededEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.UPDATE_FAILED]: logErrorEffect,
	[ACTION_TYPES.CHECKLIST_ITEM_ADD]: checkListItemAddEffect(),
	[ACTION_TYPES.CHECKLIST_ITEM_ADD_SUCCEEDED]: {
		effect: checkListItemAddSuccessEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.CHECKLIST_ITEM_ADD_FAILED]: logErrorEffect,
	[ACTION_TYPES.CHECKLIST_ITEM_REMOVE]: checkListItemRemoveEffect(),
	[ACTION_TYPES.CHECKLIST_ITEM_REMOVE_SUCCEEDED]: {
		effect: checkListItemRemoveSuccessEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.CHECKLIST_ITEM_REMOVE_FAILED]: logErrorEffect,
	[ACTION_TYPES.NOW_INPUT_VALUE_SET]: {
		effect: inputValueSetEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.NOW_INPUT_INVALID_SET]: {
		effect: inputValueInvalidSetEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.NOW_INPUT_INPUT]: {
		effect: inputValueChangeEffect,
		stopPropagation: true
	}
};
