import { ACTION_TYPES, DROPDOWN_IDS } from '../constants';

export const dropdownItemSelectedEffect = ({
	state,
	action,
	updateState,
	dispatch
}) => {
	const itemId = action.payload.item.id;
	const {
		properties: { id, checklistId }
	} = state;

	updateState({ isDropDownOpen: false, isHovering: false });
	if (itemId === DROPDOWN_IDS.EDIT_CHECKLIST_ITEM) {
		updateState({ editing: true });
	} else if (itemId === DROPDOWN_IDS.REMOVE_CHECKLIST_ITEM) {
		updateState({ isLoading: true });
		dispatch(ACTION_TYPES.CHECKLIST_ITEM_REMOVE, {
			checklistId,
			checklistItemId: id
		});
		//dispatch(ACTION_TYPES.SHIFT_FOCUS, { id });
	}
};

export const dropdownToggleEffect = ({ action, updateState }) => {
	updateState({
		isDropDownOpen: action.payload.value
	});
};

export const checkboxSetEffect = ({
	properties: { id, checklistId, completed, readOnly },
	dispatch,
	updateState
}) => {
	if (!readOnly) {
		updateState({ isLoading: true });
		dispatch(ACTION_TYPES.UPDATE, {
			id,
			checklistId,
			completed: !completed
		});
	}
};

export const inputValueSetEffect = ({
	properties: { id, label, checklistId },
	action: {
		payload: { value }
	},
	dispatch,
	updateState
}) => {
	updateItem({ dispatch, checklistId, id, value, label, updateState });
};

export const inputValueInvalidSetEffect = ({ updateState }) => {
	updateState({ editing: false });
};

export const inputValueChangeEffect = ({ action, updateState }) => {
	updateState({ value: action.payload.fieldValue });
};

export const updateItem = ({
	dispatch,
	checklistId,
	id,
	value,
	label,
	updateState
}) => {
	const inputValue = value.trim();
	if (inputValue && inputValue !== label) {
		updateState({ isLoading: true });
		dispatch(ACTION_TYPES.UPDATE, { id, checklistId, value: inputValue });
	}
	updateState({ editing: false });
};
