import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	getAPIPath,
	sendErrorNotification,
	refreshList,
	openRecord
} from '../../../../utils';
import { closeModal, showLoader, hideLoader } from '../../helperActions';
import { SCRIPT_NAMES, METHOD_NAMES } from '../../../../constants';
import {
	ACTION_TYPES,
	MODAL_MESSAGES,
	ADD_TO_EXISTING_MATTER_FAILED
} from './constants';

export const addToExistingMatterSubmitEffect = ({
	dispatch,
	updateState,
	action: {
		payload: { fieldValues }
	},
	properties: { parentRecordSysId, parentTable, sysIds }
}) => {
	showLoader(updateState);
	dispatch(ACTION_TYPES.ADD_TO_EXISTING_MATTER, {
		data: {
			parentRecordSysId,
			parentTable,
			sysIds,
			fieldValues
		}
	});
};

export const addToExistingMatter = () => {
	return createHttpEffect(
		getAPIPath(
			SCRIPT_NAMES.LEGAL_OPERATIONS,
			METHOD_NAMES.ADD_TO_EXISTING_MATTER
		),
		{
			method: 'POST',
			dataParam: 'data',
			successActionType: ACTION_TYPES.ADD_TO_EXISTING_MATTER_SUCCESS,
			errorActionType: ACTION_TYPES.ADD_TO_EXISTING_MATTER_FAILURE
		}
	);
};

export const addToExistingMatterSuccessEffect = ({
	action,
	dispatch,
	updateState
}) => {
	hideLoader(updateState);
	closeModal(updateState);
	const result = action.payload.result.success;
	if (!result.actionSuccess) {
		const errorMessageRes = result.errorMessage;
		sendErrorNotification(
			dispatch,
			errorMessageRes || MODAL_MESSAGES.CREATE_MATTER_MESSAGE
		);
	} else {
		refreshList(dispatch);
		openRecord(dispatch, { table: result.table, sysId: result.sysId });
	}
};

export const addToExistingMatterFailureEffect = ({ dispatch, updateState }) => {
	hideLoader(updateState);
	closeModal(updateState);
	sendErrorNotification(dispatch, ADD_TO_EXISTING_MATTER_FAILED);
};
