import map from 'lodash/map';
import zipObject from 'lodash/zipObject';

export const separators = Object.freeze({
	AND: 'AND',
	OR: 'OR',
	NQ: 'NQ'
});

export const uiPolicyActions = Object.freeze([
	'hidden',
	'required',
	'readonly'
]);

export const operatorConfig = Object.freeze({
	EQUAL: {
		name: '=',
		getActualValue(value) {
			return value;
		},
		evaluatePredicate(predicateValue, fieldValue) {
			return fieldValue.toString() == predicateValue;
		}
	},
	NOT_EQUAL: {
		name: '!=',
		getActualValue(value) {
			return value;
		},
		evaluatePredicate(...args) {
			return !operatorConfig.EQUAL.evaluatePredicate(...args);
		}
	},
	IN: {
		name: 'IN',
		getActualValue(value) {
			return value ? value.split(',') : [];
		},
		evaluatePredicate(predicateValue, fieldValue) {
			return predicateValue.includes(String(fieldValue));
		}
	},
	NOT_IN: {
		name: 'NOT IN',
		getActualValue(value) {
			return value ? value.split(',') : [];
		},
		evaluatePredicate(...args) {
			return !operatorConfig.IN.evaluatePredicate(...args);
		}
	},
	IS_EMPTY: {
		name: 'ISEMPTY',
		getActualValue() {
			return '';
		},
		evaluatePredicate(predicateValue, fieldValue) {
			return fieldValue === '';
		}
	},
	IS_NOT_EMPTY: {
		name: 'ISNOTEMPTY',
		getActualValue() {
			return '';
		},
		evaluatePredicate(...args) {
			return !operatorConfig.IS_EMPTY.evaluatePredicate(...args);
		}
	}
});

export const operatorNames = Object.freeze(map(operatorConfig, 'name'));
export const operatorKeys = Object.freeze(
	zipObject(operatorNames, Object.keys(operatorConfig))
);
