import { keyUtils } from '@devsnc/library-form';
import { updateItem } from './effects';
import { setFocus } from '../utils';

export default [
	{
		events: ['keydown'],
		effect({
			state,
			updateState,
			dispatch,
			action: {
				payload: { event, host }
			}
		}) {
			const {
				editing,
				value,
				properties: { label, checklistId, id }
			} = state;
			event.stopPropagation();
			if (editing) {
				keyUtils.onEnter(event, () => {
					updateItem({ dispatch, checklistId, id, value, label, updateState });
				});

				keyUtils.onEscape(event, () => {
					updateState({
						editing: false
					});
					setFocus(host);
				});
			}
		}
	}
];
