import {
	createMatterProps,
	createMatterActions,
	createMatterSubmitEffect,
	createMatterOnManageValueEffect,
	createMatterBootstrapEffect
} from './partial-components/create-matter';

import {
	addToExistingMatterProps,
	addToExistingMatterActions,
	addToExistingMatterSubmitEffect
} from './partial-components/add-to-existing-matter';

import {
	uploadRevisionProps,
	uploadRevisionActions,
	uploadRevisionBootstrapEffect,
	uploadRevisionSubmitEffect
} from './partial-components/upload-revision';

import {
	initiateApprovalProps,
	initiateApprovalActions,
	initiateApprovalBootstrapEffect,
	initiateApprovalSubmitEffect,
	initiateApprovalOnManageValueEffect,
	initiateApprovalOnChangeValueEffect
} from './partial-components/initiate-approval';

import {
	cancelApprovalProps,
	cancelApprovalActions,
	cancelApprovalBootstrapEffect,
	cancelApprovalSubmitEffect
} from './partial-components/cancel-approval';

/**
 * To define a new partial component, add it as a new property into the action schema
 * and fill out the required properties like state, actions, effects.
 */
export const actionSchema = Object.freeze({
	'create-matter': {
		state: {
			name: 'createMatter',
			value: createMatterProps
		},
		actions: createMatterActions,
		effects: {
			onManageValue: createMatterOnManageValueEffect,
			onSubmit: createMatterSubmitEffect,
			onBootstrap: createMatterBootstrapEffect
		}
	},
	'add-to-existing-matter': {
		state: {
			name: 'addToExistingMatter',
			value: addToExistingMatterProps
		},
		actions: addToExistingMatterActions,
		effects: {
			onSubmit: addToExistingMatterSubmitEffect
		}
	},
	'upload-revision': {
		state: {
			name: 'uploadRevision',
			value: uploadRevisionProps
		},
		actions: uploadRevisionActions,
		effects: {
			onBootstrap: uploadRevisionBootstrapEffect,
			onSubmit: uploadRevisionSubmitEffect
		}
	},
	'initiate-approval': {
		state: {
			name: 'initiateApproval',
			value: initiateApprovalProps
		},
		actions: initiateApprovalActions,
		effects: {
			onChange: initiateApprovalOnChangeValueEffect,
			onBootstrap: initiateApprovalBootstrapEffect,
			onSubmit: initiateApprovalSubmitEffect,
			onManageValue: initiateApprovalOnManageValueEffect
		}
	},
	'cancel-approval': {
		state: {
			name: 'cancelApproval',
			value: cancelApprovalProps
		},
		actions: cancelApprovalActions,
		effects: {
			onBootstrap: cancelApprovalBootstrapEffect,
			onSubmit: cancelApprovalSubmitEffect
		}
	}
});
