import { FLEX_RESIZE_CLASS } from './constants';

export const resizingFlex = resizingEl => {
	let lastSize;
	const resizingPercent = percent => {
		resizingEl.style.flexBasis = percent + '%';
		lastSize = percent;
	};

	const resizingFlex = (height, maxheight) => {
		const percent = (height / maxheight) * 100;
		resizingPercent(percent);
	};

	const getLastSize = () => lastSize;

	return {
		resizing: resizingFlex,
		resizeClassName: FLEX_RESIZE_CLASS,
		resizingWithRawValue: resizingPercent,
		getLastSize
	};
};
