import '@servicenow/now-loader';

const style = {
	display: 'flex',
	'justify-content': 'center',
	'align-items': 'center',
	margin: '0 5px'
};

export const itemSpinner = loadingFlag => {
	if (loadingFlag) {
		return (
			<div style={style}>
				<now-loader size="md" />
			</div>
		);
	}
	return null;
};
