import invert from 'lodash/invert';
import mapValues from 'lodash/mapValues';

/**
 * To define a new type, add it as a new property into the field schema
 * and fill out the required properties like name, actions.
 */
export const fieldSchema = Object.freeze({
	string: {
		name: 'now-input',
		props: [],
		actions: [
			{
				name: 'NOW_INPUT#INPUT',
				nameKey: 'name',
				valueKey: 'fieldValue'
			}
		]
	},
	textarea: {
		name: 'now-textarea',
		props: [],
		actions: [
			{
				name: 'NOW_TEXTAREA#INPUT',
				nameKey: 'name',
				valueKey: 'fieldValue'
			}
		]
	},
	choice: {
		name: 'sn-record-choice',
		props: ['choices'],
		actions: [
			{
				name: 'SN_RECORD_CHOICE#VALUE_CHANGED',
				nameKey: 'name',
				valueKey: 'value'
			}
		]
	},
	reference: {
		name: 'now-record-field-lookup-connected',
		props: [
			'tableName',
			'fieldName',
			'additionalQueryString',
			'multiSelection'
		],
		actions: [
			{
				name: 'NOW_RECORD_FIELD_LOOKUP_CONNECTED#VALUE_CHANGED',
				nameKey: 'name',
				valueKey: 'value',
				additionalProps: {
					displayValue: 'displayValue'
				}
			}
		]
	},
	boolean: {
		name: 'now-checkbox',
		props: [],
		actions: [
			{
				name: 'NOW_CHECKBOX#CHECKED_SET',
				nameKey: 'name',
				valueKey: 'value'
			}
		]
	},
	html: {
		name: 'now-record-html-editor',
		props: [],
		actions: [
			{
				name: 'NOW_RECORD_HTML_EDITOR#STAGED_VALUE_CHANGED',
				nameKey: 'name',
				valueKey: 'value'
			}
		]
	},
	radio: {
		name: 'now-radio-group',
		props: ['options'],
		actions: [
			{
				name: 'NOW_RADIO_GROUP#VALUE_CHANGED',
				nameKey: 'name',
				valueKey: 'value'
			}
		]
	},
	attachment: {
		name: 'sn-record-file-attachment',
		props: ['tableName', 'tableSysId'],
		actions: [
			{
				name: 'SN_RECORD_FILE_ATTACHMENT#VALUE_CHANGED',
				nameKey: 'name',
				valueKey: 'sys_id',
				additionalProps: {
					displayValue: 'file_name'
				}
			}
		]
	},
	date: {
		name: 'now-record-date-picker',
		props: ['format', 'timepicker'],
		actions: [
			{
				name: 'NOW_RECORD_DATE_PICKER#VALUE_CHANGED',
				nameKey: 'name',
				valueKey: 'value'
			}
		]
	}
});

export const commonProps = Object.freeze([
	'label',
	'name',
	'required',
	'disabled',
	'value',
	'displayValue',
	'manageValue',
	'readonly'
]);

export const validFieldTypes = Object.freeze(Object.keys(fieldSchema));
export const legacyFieldTypes = Object.freeze([]);
export const fieldTypesToComponents = Object.freeze(
	mapValues(fieldSchema, 'name')
);
export const fieldTypesToActionNames = Object.freeze(
	mapValues(fieldSchema, 'actions[0].name')
);
export const actionNamesToFieldTypes = Object.freeze(
	invert(fieldTypesToActionNames)
);
export const typeSpecificProps = Object.freeze(mapValues(fieldSchema, 'props'));
