import { findTabbableNodes } from '@servicenow/behavior-key-binding';
import { delay } from '../../utils';

export const setFocus = hostEl => {
	delay(0).then(() => {
		const activeElements = findTabbableNodes(hostEl);
		if (activeElements.length > 0) {
			activeElements[activeElements.length - 1].focus();
		}
	});
};
