import { t } from 'sn-translate';
import '@servicenow/now-modal';
import '@servicenow/now-empty-state';
import '@servicenow/now-button';
import '@servicenow/now-record-html-editor';
import '@servicenow/now-dropdown';
import { BUTTON_TYPES, DROPDOWN_ITEMS, LABELS } from './constants';

import {
	// eslint-disable-next-line no-unused-vars
	Fragment,
	dangerouslyCreateElementFromString
} from '@servicenow/ui-renderer-snabbdom';

const getFooterButtons = (instructions, newInstructions) => {
	const isEditMode = instructions === '';
	const isInstructionsFieldEmpty =
		newInstructions === '' || typeof newInstructions == 'undefined';
	const primaryButtonLabel = isEditMode
		? LABELS.ADD_BUTTON
		: LABELS.UPDATE_BUTTON;

	return [
		{
			variant: 'primary',
			label: primaryButtonLabel,
			buttonAction: BUTTON_TYPES.ADD_RECORD,
			disabled: isInstructionsFieldEmpty ? true : false
		},
		{
			variant: 'secondary',
			label: LABELS.CANCEL_BUTTON,
			buttonAction: BUTTON_TYPES.CANCEL
		}
	];
};
const getNewInstructionsContainer = (updateState, readOnly) => {
	return (
		<div className="legal-instructions-new-container">
			<p className="legal-instructions-new-label">{LABELS.NO_INSTRUCTIONS}</p>
			{readOnly ? null : (
				<now-button
					label={LABELS.ADD_BUTTON}
					size="md"
					on-click={() => updateState({ createNew: true })}
					variant="secondary"
				/>
			)}
		</div>
	);
};

const getInstructionsContainer = (instructions, updateState, readOnly) => {
	if (instructions) {
		return dangerouslyCreateElementFromString(
			`<div class="legal-instructions-text">${instructions}</div>`
		);
	} else {
		return getNewInstructionsContainer(updateState, readOnly);
	}
};

const view = (state, { updateState }) => {
	const {
		properties: { instructionTitle, readOnly },
		isLoading,
		isError,
		createNew,
		instructions,
		newInstructions
	} = state;

	const renderContent = () => {
		if (isLoading) {
			return (
				<now-empty-state
					widget={LABELS.INSTRUCTIONS}
					type="loading"
					variant="small"
				/>
			);
		} else if (isError) {
			return <now-empty-state type="empty" variant="small" />;
		} else {
			return (
				<Fragment>
					{createNew ? (
						<now-modal
							opened={createNew}
							header-label={
								instructions == ''
									? LABELS.ADD_INSTRUCTIONS
									: LABELS.EDIT_INSTRUCTIONS
							}
							footer-actions={getFooterButtons(instructions, newInstructions)}
							size="md">
							<div className="legal-instructions-container">
								<now-record-html-editor
									label={LABELS.INSTRUCTIONS}
									value={instructions}
									autofocus={true}
									onStagedValueChange={event =>
										updateState({
											newInstructions: event.value
										})
									}
									onValueChange={event =>
										updateState({
											newInstructions: event.value
										})
									}
								/>
							</div>
						</now-modal>
					) : null}
					{getInstructionsContainer(instructions, updateState, readOnly)}
				</Fragment>
			);
		}
	};

	return (
		<main>
			<header>
				{instructionTitle && (
					<h2 className="legal-instructions-title">{instructionTitle}</h2>
				)}
				<now-dropdown
					icon="ellipsis-v-outline"
					bare
					size="sm"
					hide-caret
					select="none"
					config-aria={{
						'aria-label': t('Options')
					}}
					disabled={instructions == '' || readOnly ? true : false}
					items={DROPDOWN_ITEMS}
				/>
			</header>
			{renderContent()}
		</main>
	);
};
export default view;
