import {
	dropdownItemSelectedEffect,
	dropdownToggleEffect,
	checkboxSetEffect,
	inputValueSetEffect,
	inputValueInvalidSetEffect,
	inputValueChangeEffect
} from './effects';
import { ACTION_TYPES } from '../constants';
import { actionTypes } from '@servicenow/ui-core';

export default {
	[actionTypes.COMPONENT_CONNECTED]: {
		effect({
			updateState,
			action: {
				payload: { host }
			}
		}) {
			updateState({ hostEl: host });
		}
	},

	[actionTypes.COMPONENT_DISCONNECTED]: {
		effect({ updateState }) {
			updateState({ hostEl: null });
		}
	},

	[ACTION_TYPES.NOW_DROPDOWN_ITEM_CLICKED]: {
		effect: dropdownItemSelectedEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.NOW_DROPDOWN_OPENED_SET]: {
		effect: dropdownToggleEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.NOW_CHECKBOX_CHECKED_SET]: {
		effect: checkboxSetEffect,
		stopPropagation: true
	},

	[ACTION_TYPES.NOW_INPUT_VALUE_SET]: {
		effect: inputValueSetEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.NOW_INPUT_INVALID_SET]: {
		effect: inputValueInvalidSetEffect,
		stopPropagation: true
	},
	[ACTION_TYPES.NOW_INPUT_INPUT]: {
		effect: inputValueChangeEffect,
		stopPropagation: true
	},
	[actionTypes.COMPONENT_PROPERTY_CHANGED]: {
		effect({
			action: {
				payload: { name }
			},
			updateState
		}) {
			if (name === 'completed' || name === 'label') {
				updateState({ isLoading: false });
			}
		}
	}
};
