import isEmpty from 'lodash/isEmpty';

export function refreshList(dispatch) {
	dispatch('REFRESH_REQUESTED', {
		timestamp: Date.now()
	});
}

export function openRecord(dispatch, { table, sysId }) {
	dispatch('PREVIEW_RECORD', {
		table,
		sys_id: sysId
	});
}

function buildMessage(type) {
	return function(message) {
		return {
			type,
			message
		};
	};
}

function sendListNotification(type) {
	return function(dispatch, messages) {
		!isEmpty(messages)
			? dispatch('ADD_NOTIFICATIONS', {
					notifications: Array.isArray(messages)
						? messages.map(buildMessage(type))
						: [buildMessage(type)(messages)]
			  })
			: null;
	};
}

export const sendErrorNotification = sendListNotification('error');
