import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './styles.scss';
import view from './view';
import actionHandlers from './actions';
import eventHandlers from './events';
import { ELEMENTS } from '../../../constants';

createCustomElement(ELEMENTS.SN_LEGAL_CHECKLIST_ITEM, {
	renderer: {
		type: snabbdom,
		view
	},

	initialState: {
		editing: false,
		isHovering: false,
		isDropDownOpen: false,
		value: '',
		isLoading: false,
		hostEl: null
	},

	properties: {
		id: {},
		label: {
			reflect: true
		},
		order: { default: 0 },
		completed: {
			reflect: true,
			default: false
		},
		readOnly: {},
		checklistId: {}
	},
	actionHandlers,
	eventHandlers,
	styles
});
