import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	getAPIPath,
	sendErrorNotification,
	refreshList,
	openRecord
} from '../../../../utils';
import { closeModal, showLoader, hideLoader } from '../../helperActions';
import { SCRIPT_NAMES, METHOD_NAMES } from '../../../../constants';
import {
	ACTION_TYPES,
	MODAL_MESSAGES,
	CREATE_MATTER_FAILED
} from './constants';
import get from 'lodash/get';

export const createMatterBootstrapEffect = ({
	updateState,
	dispatch,
	properties: { parentRecordSysId }
}) => {
	showLoader(updateState);
	dispatch(ACTION_TYPES.GET_DEFAULT_TEMPLATE, {
		parentRecordSysId
	});
};

export const getDefaultTemplateEffect = () => {
	return createHttpEffect(
		getAPIPath(SCRIPT_NAMES.LEGAL_OPERATIONS, METHOD_NAMES.GET_CATALOG_MAPPING),
		{
			method: 'GET',
			queryParams: ['parentRecordSysId'],
			successActionType: ACTION_TYPES.GET_DEFAULT_TEMPLATE_SUCCESS,
			errorActionType: ACTION_TYPES.GET_DEFAULT_TEMPLATE_FAILED
		}
	);
};

export const getDefaultTemplateSuccessEffect = ({ action, updateState }) => {
	hideLoader(updateState);
	const result = action.payload.result;
	if (result && result.template) {
		const { value, displayValue } = result.template;
		updateState({
			path: `createMatter.fields[1].value`,
			value,
			operation: 'set'
		});
		updateState({
			path: `createMatter.fields[1].displayValue`,
			value: displayValue,
			operation: 'set'
		});
	}
};

export const getDefaultTemplateFailedEffect = ({ updateState }) => {
	hideLoader(updateState);
};

export const createMatterSubmitEffect = ({
	dispatch,
	updateState,
	state,
	action: {
		payload: { fieldValues }
	},
	properties: { parentRecordSysId, parentTable, sysIds }
}) => {
	showLoader(updateState);
	const templateChoice = fieldValues.withOrWithoutTemplate;
	dispatch(ACTION_TYPES.CREATE_MATTER, {
		data: {
			parentRecordSysId,
			parentTable,
			sysIds,
			fieldValues: {
				...fieldValues,
				template:
					templateChoice === 'withoutTemplate'
						? ''
						: get(state, 'createMatter.fields[1].value')
			}
		}
	});
};

export const createMatterOnManageValueEffect = ({
	updateState,
	action: {
		payload: { fieldName, value, additionalValues }
	}
}) => {
	if (fieldName === 'template') {
		updateState({
			path: `createMatter.fields[1].value`,
			value,
			operation: 'set'
		});

		updateState({
			path: `createMatter.fields[1].displayValue`,
			value: additionalValues.displayValue,
			operation: 'set'
		});
	}
};

export const createMatter = () => {
	return createHttpEffect(
		getAPIPath(SCRIPT_NAMES.LEGAL_OPERATIONS, METHOD_NAMES.CREATE_MATTER),
		{
			method: 'POST',
			dataParam: 'data',
			successActionType: ACTION_TYPES.CREATE_MATTER_SUCCESS,
			errorActionType: ACTION_TYPES.CREATE_MATTER_FAILURE
		}
	);
};

export const createMatterSuccessEffect = ({
	action,
	dispatch,
	updateState
}) => {
	hideLoader(updateState);
	closeModal(updateState);
	const result = action.payload.result.success;
	if (!result.actionSuccess) {
		var errorMessageRes = result.errorMessage;
		sendErrorNotification(
			dispatch,
			errorMessageRes || MODAL_MESSAGES.CREATE_MATTER_MESSAGE
		);
	} else {
		refreshList(dispatch);
		openRecord(dispatch, { table: result.table, sysId: result.sysId });
	}
};

export const createMatterFailureEffect = ({ dispatch, updateState }) => {
	hideLoader(updateState);
	closeModal(updateState);
	sendErrorNotification(dispatch, CREATE_MATTER_FAILED);
};
