import { createHttpEffect } from '@servicenow/ui-effect-http';
import { getAPIPath, refreshList } from '../../../../utils';
import {
	closeModal,
	showLoader,
	hideLoader,
	showAlert
} from '../../helperActions';
import { BASE_API, METHOD_NAMES, SCRIPT_NAMES } from '../../../../constants';

import {
	MESSAGE_CANCEL_REQUEST_FAILED,
	CANCEL_APPROVAL,
	CANCEL_APPROVAL_FAILED,
	CANCEL_APPROVAL_SUCCESS,
	GET_PENDING_APPROVAL,
	GET_PENDING_APPROVAL_SUCCESS,
	GET_PENDING_APPROVAL_FAILED
} from './constants';

export const cancelApprovalBootstrapEffect = ({
	dispatch,
	properties: { parentRecordSysId, parentTable }
}) => {
	dispatch(GET_PENDING_APPROVAL, {
		data: {
			parentRecordSysId,
			parentTable
		}
	});
};

export const getPendingApproval = () => {
	return createHttpEffect(
		getAPIPath(
			SCRIPT_NAMES.LEGAL_ADHOC_APPROVAL,
			METHOD_NAMES.GET_APPROVALS,
			BASE_API
		),
		{
			method: 'POST',
			dataParam: 'data',
			successActionType: GET_PENDING_APPROVAL_SUCCESS,
			errorActionType: GET_PENDING_APPROVAL_FAILED
		}
	);
};

export const getPendingApprovalSuccessEffect = ({ action, updateState }) => {
	hideLoader(updateState);

	if (action.payload.result && action.payload.result.success) {
		const actionSuccess = action.payload.result.success.actionSuccess;
		if (!actionSuccess) {
			let errorMessageRes = action.payload.result.success.errorMessage;

			showAlert(updateState, 'cancelApproval', 'warning', errorMessageRes);
		} else {
			updateState({
				path: 'cancelApproval.fields[0].additionalQueryString',
				operation: 'set',
				value: 'sys_idIN' + action.payload.result.success.sysIds
			});
		}
	}
};

export const getPendingApprovalFailureEffect = ({ updateState }) => {
	hideLoader(updateState);
	closeModal(updateState);
	showAlert(
		updateState,
		'cancelApproval',
		'warning',
		MESSAGE_CANCEL_REQUEST_FAILED
	);
};

export const cancelApprovalSubmitEffect = ({
	dispatch,
	updateState,
	action: {
		payload: { fieldValues }
	},
	properties: { parentRecordSysId, parentTable }
}) => {
	showLoader(updateState);

	dispatch(CANCEL_APPROVAL, {
		data: {
			parentRecordSysId,
			parentTable,
			fieldValues
		}
	});
};

export const cancelApproval = () => {
	return createHttpEffect(
		getAPIPath(
			SCRIPT_NAMES.LEGAL_ADHOC_APPROVAL,
			METHOD_NAMES.CANCEL_APPROVAL,
			BASE_API
		),
		{
			method: 'POST',
			dataParam: 'data',
			successActionType: CANCEL_APPROVAL_SUCCESS,
			errorActionType: CANCEL_APPROVAL_FAILED
		}
	);
};

export const cancelApprovalSuccessEffect = ({
	action,
	dispatch,
	updateState
}) => {
	hideLoader(updateState);

	if (action.payload.result && action.payload.result.success) {
		const actionSuccess = action.payload.result.success.actionSuccess;
		if (!actionSuccess) {
			let errorMessageRes = action.payload.result.success.errorMessage;

			showAlert(updateState, 'cancelApproval', 'warning', errorMessageRes);
		} else {
			closeModal(updateState);
			refreshList(dispatch);
		}
	}
};

export const cancelApprovalFailureEffect = ({ updateState }) => {
	hideLoader(updateState);
	closeModal(updateState);
	showAlert(
		updateState,
		'cancelApproval',
		'warning',
		MESSAGE_CANCEL_REQUEST_FAILED
	);
};
