import { addClassName, removeClassName } from '../../utils';
import { keyUtils } from '@devsnc/library-form';
import { resizingFlex } from './resizingFlex';
import {
	RESIZABLE_BASE_CLASS,
	RESIZING_STATE_CLASS,
	RESIZABLE_CONTAINER,
	RESIZABLE_TOP_ROW,
	KEY_SIZE
} from './constants';

let lastSizeRatio = {};

export const getLastSizeRatio = (key, defaultValue) => {
	const ratio = lastSizeRatio[key];
	return ratio !== undefined ? ratio : defaultValue;
};

const setLastSizeRatio = (key, value) => {
	if (!key) {
		return;
	}

	lastSizeRatio[key] = value;
};

export const createResizableController = (
	containerEl,
	topRowEl,
	resizeBarEl,
	resizeKey,
	options = {}
) => {
	let isResizing = false;
	let topRowHeight = 0;
	let maxResizableHeight = 0;
	let resizingAlg;
	let prevY = 0;

	const {
		minTopRowHeight = 0,
		minBottomRowHeight = 0,
		allowKeyAccess = false,
		afterStopResizing = () => {}
	} = options;

	const keyPress = event => {
		keyUtils.onEnter(event, () => {
			startResizing();
			resizeBarEl.addEventListener('keydown', resizingKeyDown);
		});
	};

	const resizingKeyDown = event => {
		keyUtils.onLeftArrow(event, () => {
			event.preventDefault();
			resizing(-KEY_SIZE);
		});

		keyUtils.onRightArrow(event, () => {
			event.preventDefault();
			resizing(KEY_SIZE);
		});

		if (keyUtils.onEscape(event) || keyUtils.onTab(event)) {
			resizeBarEl.removeEventListener('keydown', resizingKeyDown);
			stopResizing();
		}
	};

	const mouseDown = event => {
		event.preventDefault();
		event.stopPropagation();
		prevY = event.screenY;
		startResizing();
		addMouseResizingEvents();
	};

	const mouseMove = event => {
		if (!isResizing) {
			return;
		}

		let { movementY } = event;
		if (movementY === undefined) {
			movementY = prevY ? event.screenY - prevY : 0;
		}
		prevY = event.screenY;

		if (!movementY) {
			return;
		}

		requestAnimationFrame(() => {
			resizing(movementY);
		});
		event.preventDefault();
	};

	const mouseUp = () => {
		if (!isResizing) {
			return;
		}

		stopResizing();
		removeMouseResizingEvents();
	};

	const startResizing = () => {
		isResizing = true;
		topRowHeight = topRowEl.getBoundingClientRect().height;
		maxResizableHeight = containerEl.getBoundingClientRect().height;
		if (!maxResizableHeight) {
			return;
		}
		addClassName(topRowEl, RESIZING_STATE_CLASS);
		addClassName(containerEl, RESIZING_STATE_CLASS);
		requestAnimationFrame(() => {
			resizing(0);
		});
	};

	const addMouseResizingEvents = () => {
		document.addEventListener('mousemove', mouseMove);
		document.addEventListener('mouseup', mouseUp);
	};

	const removeMouseResizingEvents = () => {
		document.removeEventListener('mousemove', mouseMove);
		document.removeEventListener('mouseup', mouseUp);
	};

	const resizing = (movementY = 0) => {
		if (!isResizing) {
			return;
		}
		const newHeight = topRowHeight + movementY;
		if (isInResizeBounds(newHeight)) {
			topRowHeight = newHeight;
			resizingAlg.resizing(topRowHeight, maxResizableHeight);
		}
	};

	const stopResizing = () => {
		if (!isResizing) {
			return;
		}

		const lastSize = resizingAlg.getLastSize();
		setLastSizeRatio(resizeKey, lastSize);
		isResizing = false;
		removeClassName(containerEl, RESIZING_STATE_CLASS);
		afterStopResizing(lastSize);
	};

	const isInResizeBounds = height => {
		return (
			height <= maxResizableHeight - minBottomRowHeight &&
			height >= minTopRowHeight
		);
	};

	const addListeners = () => {
		if (!hasValidElements()) {
			// eslint-disable-next-line no-console
			console.warn('Invalid elements:', containerEl, topRowEl, resizeBarEl);
			return;
		}
		resizeBarEl.addEventListener('mousedown', mouseDown);
		if (allowKeyAccess) {
			resizeBarEl.addEventListener('focus', focusOnBar);
			resizeBarEl.addEventListener('blur', blurOnBar);
		}
	};

	const focusOnBar = () => {
		resizeBarEl.addEventListener('keypress', keyPress);
	};

	const blurOnBar = () => {
		resizeBarEl.removeEventListener('keypress', keyPress);
	};

	const removeListeners = () => {
		resizeBarEl.removeEventListener('mousedown', mouseDown);
		resizeBarEl.removeEventListener('focus', focusOnBar);
		if (allowKeyAccess) {
			resizeBarEl.removeEventListener('blur', blurOnBar);
			resizeBarEl.removeEventListener('keypress', keyPress);
		}
	};

	const hasValidElements = () => {
		return containerEl && topRowEl && resizeBarEl;
	};

	const initialize = () => {
		if (!hasValidElements()) {
			return;
		}

		resizingAlg = resizingFlex(topRowEl);
		addClassName(containerEl, RESIZABLE_BASE_CLASS);
		addClassName(topRowEl, RESIZABLE_BASE_CLASS);
		addClassName(containerEl, RESIZABLE_CONTAINER);
		addClassName(topRowEl, RESIZABLE_TOP_ROW);
		addClassName(containerEl, resizingAlg.resizeClassName);
		addClassName(topRowEl, resizingAlg.resizeClassName);
		addListeners();
	};

	const destroy = () => {
		removeListeners();
		topRowEl.removeAttribute('style');
		removeClassName(containerEl, RESIZING_STATE_CLASS);
		removeClassName(containerEl, RESIZABLE_BASE_CLASS);
		removeClassName(topRowEl, RESIZING_STATE_CLASS);
		removeClassName(topRowEl, RESIZABLE_BASE_CLASS);
		removeClassName(containerEl, RESIZABLE_CONTAINER);
		removeClassName(topRowEl, RESIZABLE_TOP_ROW);
		removeClassName(containerEl, resizingAlg.resizeClassName);
		removeClassName(topRowEl, resizingAlg.resizeClassName);
	};

	const disableResizing = () => {
		removeClassName(topRowEl, RESIZING_STATE_CLASS);
	};

	const enableResizing = () => {
		addClassName(topRowEl, RESIZING_STATE_CLASS);
	};

	const setHeight = height => {
		resizingAlg.resizingWithRawValue(height);
	};

	return {
		initialize,
		destroy,
		setHeight,
		disableResizing,
		enableResizing
	};
};
