import '@servicenow/now-alert';

const getIcon = type => {
	let iconTypes = {
		info: 'info-circle-outline',
		warning: 'exclamation-triangle-outline',
		positive: 'check-circle-outline'
	};

	return iconTypes[type] || 'info-circle-outline';
};

export const renderAlertMessage = (
	showAlert,
	{ type = 'info', message = '' } = {}
) => {
	return (
		<div>
			<now-alert
				status={type}
				icon={getIcon(type)}
				header=""
				content={message}
				textLinkProps={{}}
				action={{ type: 'dismiss' }}
				style={{ display: showAlert ? 'block' : 'none' }}
			/>
		</div>
	);
};
