import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './styles.scss';
import { ELEMENTS } from '../../constants';
import actionHandlers from './actions';
import view from './view';

createCustomElement(ELEMENTS.SN_LEGAL_INSTRUCTIONS, {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		instructions: '',
		isLoading: true,
		isError: false,
		createNew: false,
		newInstructions: ''
	},
	properties: {
		instructionTitle: {
			reflect: true
		},
		readOnly: {
			default: false
		},
		//record sys id to which the checklist is attached to (example task sys id)
		sysId: {
			reflect: true
		}
	},
	actionHandlers,
	styles
});
